<template>
  <div class="header clearfix">
    <!--  -->
    <div class="left fl">
      <div class="logo fl"></div>
      <p class="companyName fl"></p>
    </div>
    <!--  -->

    <!--  -->
    <div class="right fr">
      <el-dropdown>
        <div class="el-dropdown-link">
          <img class="avatar" :src="!!headImgFileId ? downloadUrl + headImgFileId : defaultAvatar"/>
          <span>{{role.roleName}}</span>
        </div>
        <el-dropdown-menu slot="dropdown">
          <!--<el-dropdown-item>-->
            <!--<div class="item">-->
              <!--<div class="icon"></div>-->
              <!--<div class="changePwd" @click="toggleReset"></div>-->
            <!--</div>-->
          <!--</el-dropdown-item>-->
          <el-dropdown-item>
            <div class="item">
              <div class="icon"></div>
              <div class="changePwd" @click="exitPage"></div>
            </div>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <!--  -->

    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowReset"
      :toggleContent="toggleReset"
      :confirmDialog="submitReset"
    >
      <div class="resetPwd">

        <commonTextInput
          :text="''"
          v-model="originPwd"
        >
        </commonTextInput>

        <commonTextInput
          :text="''"
          v-model="newPwd"
          :errorTips="'8-16'"
          :rexpRule="/^[0-9A-Za-z]{8,16}$/"
        >
        </commonTextInput>

        <commonTextInput
          :text="''"
          v-model="confirmPwd"
          :errorTips="''"
          :errorState="errorState"
        >
        </commonTextInput>

      </div>
    </assignAlertDialog>
    <!--  -->
  </div>
</template>

<script>
  import assignAlertDialog from '@/components/assignAlertDialog';
  import commonTextInput from '@/components/commonTextInput';

  export default {
    name: "commonHeader",
    components: {
      assignAlertDialog,
      commonTextInput
    },
    data() {
      return {
        isShowReset: false,         // 
        originPwd: '',              // 
        newPwd: '',                 // 
        confirmPwd: '',              // 
        errorState: false,           // 
        headImgFileId: '',           // ID
        downloadUrl: this.$downloadUrl, // 
        defaultAvatar: this.$defaultAvatar,
        role:{}                      // 
      }
    },
    watch: {
      newPwd(val) {
        if (val !== this.confirmPwd) {
          this.errorState = true;
        } else {
          this.errorState = false;
        }
      },
      confirmPwd(val) {
        if (val !== this.newPwd) {
          this.errorState = true;
        } else {
          this.errorState = false;
        }
      },
      statusSet(val){
        if(val){
          this.initData()
        }
      }
    },
    computed:{
      statusSet(){
        return this.$store.state.isLogin
      }
    },
    methods: {
      /**
       * 
       * @param fag
       */
      toggleReset(fag) {
        if (fag === -1) {
          this.isShowReset = false;
        } else {
          this.isShowReset = !this.isShowReset;
        }
      },
      /**
       * 
       */
      submitReset() {
        // console.log('');
        this.toggleReset(-1)
      },
      /**
       * 
       */
      exitPage() {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          center: true
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.$localStore.removeItem('auth-token');
          this.$localStore.removeItem('authority');
          this.$localStore.removeItem('info');
          this.$localStore.removeItem('role');
          location.reload()
        })

      },
      initData(){
        let info = JSON.parse(this.$localStore.getItem('role'));
        if(!!JSON.parse(this.$localStore.getItem('info'))){
          let headImgFileId = JSON.parse(this.$localStore.getItem('info')).headImgFileId;
          // 
          if(headImgFileId)this.headImgFileId = headImgFileId;
        }
        if(!!info) this.role = info;
      }
    },
    created() {
      this.initData()
    }
  }
</script>

<style lang="scss">
  .header {
    background-color: #4486FF;
    line-height: 60px;
    padding: 0 20px;
    width: 100%;
    box-sizing: border-box;
    .left {
      /*.logo{*/
      /*width: 25px;*/
      /*height: 25px;*/
      /*background-color: skyblue;*/
      /*border-radius: 50%;*/
      /*}*/
      .companyName {
        font-weight: bold;
        font-size: 25px;
        color: #ffffff;
      }
    }

    .right {
      font-size: 14px;

      .el-dropdown {
        color: #ffffff;
      }

      .avatar {
        margin-left: 20px;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        border: 1px solid #ffffff;
        display: inline-block;
        vertical-align: middle;
        background-size: contain;
      }

      .icon {
        display: inline-block;
      }
    }

    .changePwd {
      cursor: pointer;
    }

    .resetPwd {
      padding: 40px 90px 0 60px;

      .iconText {
        width: 70px;
      }
    }
  }
</style>



