<template>
  <div class="assignSignerInfo">
    <p class="assignSignerTitle">
      <span class="title"></span>
    </p>
    <div class="signServiceInfo">
      <div class="serviceTableRow">
        <div class="serviceTableCell name"></div>
        <div class="serviceTableCell detail">{{signerInfo.DJJGMC}}</div>
      </div>
      <div class="serviceTableRow">
        <div class="serviceTableCell name"></div>
        <div class="serviceTableCell detail">{{signerInfo.TDMC}}</div>
      </div>
      <div class="serviceTableRow">
        <div class="serviceTableCell name"></div>
        <div class="serviceTableCell detail">
          0
          <!--<a href="#" class="lookDetail" @click.stop.prevent="toggleMainContent"></a>-->
        </div>
      </div>
      <div class="serviceTableRow">
        <div class="serviceTableCell name"></div>
        <div class="serviceTableCell detail">{{signerInfo.CREATETIME}}</div>
      </div>
      <div class="serviceTableRow">
        <div class="serviceTableCell name"></div>
        <div class="serviceTableCell detail">{{signerInfo.SOURCE}}</div>
      </div>
      <div class="serviceTableRow">
        <div class="serviceTableCell name"></div>
        <div class="serviceTableCell detail">{{signerInfo.QYRQ}}<i></i>{{signerInfo.DQRQ}}</div>
      </div>
      <div class="serviceTableRow">
        <div class="serviceTableCell name"></div>
        <div class="serviceTableCell detail">{{signerInfo.JYRQ}}</div>
      </div>
      <div class="serviceTableRow">
        <div class="serviceTableCell name"></div>
        <div class="serviceTableCell detail"><span>{{signerInfo.ORDERSTATUSNAME}}</span></div>
      </div>
      <div class="serviceTableRow">
        <div class="serviceTableCell name"></div>
        <div class="serviceTableCell detail">{{signerInfo.JYYY}}</div>
      </div>
      <div class="serviceTableRow">
        <div class="serviceTableCell name"></div>
        <div class="serviceTableCell detail">
          <a href="#"
             class="print highlight"
             v-if="signStatus=== 1"
             @click.prevent.stop="printPdf"
          >
            
            <i class="printing"></i>
          </a>
          <span class="print" v-if="signStatus !== 1"> <i class="printed"></i></span>
        </div>
      </div>
    </div>
    <div class="btns" v-if="isShowSign">
      <el-button type="primary" class="major" v-if="signStatus === 0" @click.prevent.stop="toggleSignDialog">
      </el-button>
      <el-button type="danger" v-if="signStatus === 0" @click.prevent.stop="toggleRejctDialog" class="reject"></el-button>
      <el-button type="info" v-if="signStatus === 2" plain disabled class="refused"></el-button>
      <el-button type="info" v-if="signStatus === 3" plain disabled class="canceled"></el-button>
      <el-button type="info" v-if="signStatus === 4" plain disabled class="retracted"></el-button>
      <el-button type="info" v-if="signStatus === 5" plain disabled class="overTimed"></el-button>
      <el-button type="danger" class="cancel" v-if="signStatus === 1" @click.prevent.stop="toggleTerminateDialog">
      </el-button>
    </div>
    <!--  -->

    <assignChargeInfo
      :isShow="isShowServicePackage"
      :toggleMainContent='toggleMainContent'
    >
    </assignChargeInfo>
    <!--  -->

    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowSignFile"
      :toggleContent="toggleSignDialog"
      :confirmDialog="confirmRetract"
    >
      <div class="paddingBox"></div>
    </assignAlertDialog>
    <!--  -->

    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowReject"
      :toggleContent="toggleRejctDialog"
      :confirmDialog="refuseRetract"
    >
      <div class="paddingBox nonePaddingBottom">
        <commonSelector :options="rejectList" v-model="rejectReason" :text="''"></commonSelector>
      </div>
    </assignAlertDialog>
    <!--  -->

    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowTerminate"
      :toggleContent="toggleTerminateDialog"
      :confirmDialog="retractFile"
    >
      <div class="paddingBox nonePaddingBottom">
        <commonTextArea v-model="JYYY" :text="''"></commonTextArea>
      </div>
    </assignAlertDialog>
    <!--  -->

    <!--  -->
    <iframe class="myIframe" frameborder=0 name="showHere" scrolling=auto src="">
      <pdfPrint
        ref="print"
        :PARTYA="pdfInfo.PARTYA"
        :PARTYB="pdfInfo.PARTYB"
        :SFZJH="pdfInfo.SFZJH"
        :LXDH="pdfInfo.LXDH"
        :JTZZ="pdfInfo.JTZZ"
        :TDMC="pdfInfo.TDMC"
        :TDCYList="pdfInfo.TDCYList"
        :TDZRYS="pdfInfo.TDZRYS"
        :TDLXDH="pdfInfo.TDLXDH"
        :QYFWList="pdfInfo.QYFWList"
        :JTCYList="pdfInfo.JTCYList"
      >
      </pdfPrint>
    </iframe>
    <!--  -->
  </div>
</template>

<script>
  import commonDialog from './commonDialog';                             // 
  import assignChargeInfo from './assignChargeInfo';                     // 
  import assignAlertDialog from './assignAlertDialog';                  // /
  import commonSelector from './commonSelector';                        // 
  import commonTextArea from './commonTextArea';
  import pdfPrint from '@/components/pdfPrint';                          // pdf

  const APPROVAL_LIST = [0, 1, 2];                                // 
  let timer = 'pdfTimer';                                       //

  export default {
    name: "assignSignerInfo",
    components: {
      assignChargeInfo,
      commonDialog,
      assignAlertDialog,
      commonSelector,
      commonTextArea,
      pdfPrint
    },
    data() {
      return {
        isShowServicePackage: false,          // 
        isShowSignFile: false,                // 
        isShowReject: false,                  // 
        isShowTerminate: false,               // 
        rejectList: [{                        // List
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }
        ],
        rejectReason: '',                    // 
        terminationPerson: '',               // 
        terminationList: [                   // 
          {
            value: '1',
            label: ''
          },
          {
            value: '2',
            label: ''
          }
        ],
        JYYY: '',                        // 
        pdfInfo: {}
      }
    },
    props: {
      signerInfo: {
        default: () => {
        }
      },         // 
      QYID: '',                             // ID
      signStatus: {default: 0},            //  012345
      isShowSign: {default: false}           // 
    },
    methods: {
      /**
       * 
       * @param fag
       */
      toggleMainContent(fag) {
        // if (fag === -1) {
        //   this.isShowServicePackage = false;
        // } else {
        //   this.isShowServicePackage = !this.isShowServicePackage;
        // }
      },
      /**
       * 
       * @param fag type:string
       **/
      toggleSignDialog(fag) {
        if (fag === -1) {
          this.isShowSignFile = false;
        } else {
          this.isShowSignFile = !this.isShowSignFile;
        }
      },
      /**
       * 
       * @param fag type:string
       **/
      toggleRejctDialog(fag) {
        if (fag === -1) {
          this.isShowReject = false;
        } else {
          this.isShowReject = !this.isShowReject;
        }
      },
      /**
       * 
       * @param fag type:string
       **/
      toggleTerminateDialog(fag) {
        if (fag === -1) {
          this.isShowTerminate = false;
        } else {
          this.isShowTerminate = !this.isShowTerminate;
        }
      },
      /**
       * 
       */
      confirmRetract() {
        this.$emit('setEvent', {val: 1, callback: this.toggleSignDialog});
      },
      /**
       * 
       */
      refuseRetract() {
        this.$emit('setEvent', {val: 2, res: this.rejectReason, callback: this.toggleRejctDialog});
      },
      /**
       * 
       */
      retractFile() {
        const {QYID, JYYY} = this;
        this.$post('family/sign/order/cancelSignFamily',{QYID, JYYY}).then(rsp=>{
          this.$emit('setSignStatus', 4);
          this.toggleTerminateDialog();
          // this.$emit('initData');
          this.$emit('closeDialog');
        }, rej=>{
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }
        })
      },
      /**
       * 
       */
      printPdf(){
        const {QYID} = this;
        this.$post('family/sign/order/buildPdf',{QYID}).then(rsp=>{
          this.pdfInfo = rsp.data;
          clearTimeout(timer);
          timer = setTimeout(()=>{
            this.$print(this.$refs.print.$el);
          }, 1000);
        }, rej=>{
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }
        });
      }
    }
  }
</script>

<style lang="scss">
  .assignSignerInfo {
    .paddingBox {
      padding: 30px 0;
    }
    .myIframe{
      height: 0;
    }

    .nonePaddingBottom {
      padding-bottom: 0;

      .commonSelector {
        display: flex;
        line-height: 30px;

        .iconText {
          width: 80px;
          text-align: right;
        }
      }
    }

    .assignSignerTitle {
      text-align: left;
      font-weight: bold;
      font-size: 18px;
      padding-top: 28px;
      padding-bottom: 20px;
    }

    .signServiceInfo {
      display: table;
      border-top: 1px solid #DDDDDD;
      border-left: 1px solid #DDDDDD;
      box-sizing: border-box;

      .serviceTableRow {
        display: table-row;

        .serviceTableCell {
          height: 50px;
          line-height: 50px;
          text-align: center;
          font-size: 16px;
          border-right: 1px solid #DDDDDD;
          border-bottom: 1px solid #DDDDDD;
          box-sizing: border-box;
          display: table-cell;

          &.name {
            width: 114px;
          }

          &.detail {
            width: 1178px;
            text-align: left;
            text-indent: 23px;
          }

          .lookDetail {
            color: #29CB97;
            margin-left: 11px;
          }

          .print {
            color: #4486FF;
            opacity: 0.3;
            .printing{
              height: 16px;
              width: 16px;
              display: inline-block;
              vertical-align: middle;
              background: url("../assets/ic_printing_on.png") no-repeat;
              background-size: contain;
            }
            .printed{
              height: 16px;
              width: 16px;
              display: inline-block;
              vertical-align: middle;
              background: url("../assets/ic_printing_off.png") no-repeat;
              background-size: contain;
            }

            &.highlight {
              opacity: 1;
            }
          }
        }
      }
    }

    .btns {
      padding: 30px 0;
      text-align: left;

      .major {
        margin-right: 20px !important;
        background-color: #4486ff;
      }
      .reject{
        background-color: #F65860;
      }

      .el-button {
        padding: 0 0;
        margin: 0 0;
        width: 86px;
        height: 30px;
        line-height: 30px;
      }
      .refused, .canceled, .retracted, .overTimed {
        color: #F65860;
        line-height: 30px;
      }
    }

  }
</style>



