<template>
  <div class="assignDialog">
    <!--  -->

    <!--  -->
    <div class="assignDialogTitle">
      <span></span>
      <div class="closeIcon" @click.stop="closeContent">&times;</div>
    </div>
    <!--  -->

    <!--  -->
    <div class="assignDialogContent">
      <p class="contentTitle">></p>
      <div class="fileList clearfix">
        <div v-for="(item, index) in fileList"
             :key="index"
             class="file fl"
             :class="{active: index=== activeIndex}"
             @click.stop="toggleFile(index)"
        >
          {{item}}
        </div>
      </div>
      <!--  -->

      <!--  -->
      <div class="createNewFile" v-if="activeIndex === 0">
        <!--  -->
        <commonToggle
          v-for="(item, index) in familyInfo.JTCYJKDA"
          :title="index===0 ? '' : ''+(index+1)"
          :isDefaultShow="index===0"
          :key="index"
        >
          <assignBaseInfo :isShowBorder="index !== familyInfo.JTCYJKDA.length-1" :memberInfo="item"
                          :isDisabled="isDisabledHealths"></assignBaseInfo>
        </commonToggle>
        <div class="btns" v-if="APPROVAL_LIST.indexOf(ORDERSTATUS) !== -1 && sign_audit">
        <!--<div class="btns" v-if="sign_audit">-->
          <el-button v-if="healthStatus === 0" type="primary" class="major" @click.prevent.stop="toggleHealthDialog">
            
          </el-button>
          <el-button v-if="healthStatus === 0" class="refuse" type="danger" @click.prevent.stop="toggleRejctDialog"></el-button>
          <span v-if="healthStatus === 1" class="established"></span>
          <span v-if="healthStatus === 2" class="refused"></span>
        </div>
        <!--  -->
      </div>
      <!--  -->

      <!--  -->
      <div class="createHomeFile" v-if="activeIndex === 1">
        <div class="homeFileHeader">
          <p class="fileName"></p>
          <!--<el-button type="primary" class="major" v-if="!isDisabledFamily" @click.prevent.stop="changeEditState">{{isEditHomeFile ?-->
          <!--'' : ''}}-->
          <!--</el-button>-->
        </div>
        <div class="baseInfo">
          <commonTextInput v-model="JDJGMC" :text="''" :isDisabled="isDisabledFamily || isHandleFamily"></commonTextInput>
          <commonTextInput v-model="GDJGMC" :text="''" :isDisabled="isDisabledFamily || isHandleFamily"></commonTextInput>
          <commonTextInput v-model="JDRMC" :text="''" :isDisabled="isDisabledFamily || isHandleFamily"></commonTextInput>
          <commonTextInput v-model="ZRYSMC" :text="''" :isDisabled="isDisabledFamily || isHandleFamily"></commonTextInput>
        </div>
        <commonAreaSelector
          :text="''"
          :province.sync="isDisabledFamily || isHandleFamily ? PROVINCEDNAME : PROVINCEDID"
          :city.sync="isDisabledFamily || isHandleFamily ? CITYNAME : CITYID"
          :area.sync="isDisabledFamily || isHandleFamily ? DISTRICTNAME : DISTRICTID"
          :street.sync="isDisabledFamily || isHandleFamily ? STREETNAME : STREETID"
          :village.sync="isDisabledFamily || isHandleFamily ? COMMITTEENAME : COMMITTEEID"
          :address.sync="XXDZ"
          :isDisabled="isDisabledFamily || isHandleFamily"
        >
        </commonAreaSelector>
        <commonAreaSelector
          :text="''"
          :province.sync="isDisabledFamily || isHandleFamily ? PROVINCEDNAME : PROVINCEDID"
          :city.sync="isDisabledFamily || isHandleFamily ? CITYNAME : CITYID"
          :area.sync="isDisabledFamily || isHandleFamily ? DISTRICTNAME : DISTRICTID"
          :street.sync="isDisabledFamily || isHandleFamily ? STREETNAME : STREETID"
          :village.sync="isDisabledFamily || isHandleFamily ? COMMITTEENAME : COMMITTEEID"
          :address.sync="XXDZ"
          :isDisabled="isDisabledFamily || isHandleFamily"
        >
        </commonAreaSelector>
        <p class="fileName memberTitle"></p>
        <div class="members">
          <assignMember
            v-for="(item, index) in familyInfo.JTCYJKDA"
            :isClose="index !== 0 && !isDisabledFamily && !isHandleFamily"
            :memberInfo="item"
            :key="index"
          >
          </assignMember>
        </div>
        <div class="btns" v-if="APPROVAL_LIST.indexOf(ORDERSTATUS) !== -1 && sign_audit">
        <!--<div class="btns" v-if="sign_audit">-->
          <el-button v-if="familyStatus === 0" type="primary" class="major" @click.prevent.stop="toggleHomeDialog">
          </el-button>
          <el-button v-if="familyStatus === 0" class="refuse" type="danger" @click.prevent.stop="toggleRejctFamilyDialog">
          </el-button>
          <span v-if="familyStatus === 1" class="established"></span>
          <span v-if="familyStatus === 2" class="refused"></span>
        </div>
      </div>
      <!--  -->

      <!--  -->
      <div class="signMembersInfo" v-if="activeIndex === 2">
        <assignSigner
          :isDisabledSign="isDisabledSign"
          :categoryList="familyInfo.RQFLJH"
          v-for="(item, index) in familyInfo.QYCYXQ.QYMXLBList"
          :key="index"
          :signerInfo="item"
          :QYID="QYID"
          :index="index"
          :orderStatus="item.ORDERSTATUS"
          @initData="initData"
          @closeDialog="closeContent"
          @setSignStatus="setSignStatus"
          :title="index === 0 ? '' : ''+(index+1)"
        >
        </assignSigner>
        <assignSignerInfo
          :isShowSign="sign_audit"
          :signerInfo="familyInfo.QYCYXQ"
          :signStatus="signStatus"
          :QYID="QYID"
          @setEvent="toggleSign"
          @setSignStatus="setSignStatus"
          @initData="initData"
          @closeDialog="closeContent"
        >
        </assignSignerInfo>
      </div>
      <!--  -->

      <!--  -->
    </div>
    <!--  -->

    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowReject"
      :toggleContent="toggleRejctDialog"
      :confirmDialog="confirmRefuseHealths"
    >
      <div class="paddingBox nonePaddingBottom">
        <commonSelector :options="rejectList" v-model="rejectReason" :text="''"></commonSelector>
      </div>
    </assignAlertDialog>
    <!--  -->

    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowFamilyReject"
      :toggleContent="toggleRejctFamilyDialog"
      :confirmDialog="confirmRefuseFamily"
    >
      <div class="paddingBox nonePaddingBottom">
        <commonSelector :options="rejectList" v-model="rejectFamilyReason" :text="''"></commonSelector>
      </div>
    </assignAlertDialog>
    <!--  -->

    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowHealthFile"
      :toggleContent="toggleHealthDialog"
      :confirmDialog="confirmEstablishHealths"
    >
      <div class="paddingBox"></div>
    </assignAlertDialog>
    <!--  -->

    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowHomeFile"
      :toggleContent="toggleHomeDialog"
      :confirmDialog="confirmEstablishFamily"
    >
      <div class="paddingBox"></div>
    </assignAlertDialog>
    <!--  -->

    <!--  -->
  </div>
</template>

<script>
  import assignBaseInfo from './assignBaseInfo';                                  // 
  import commonTextInput from './commonTextInput';                                // 
  import commonAreaSelector from './commonAreaSelector';                          // 
  import assignMember from './assignMember';                                      // 
  import assignSigner from './assignSigner';                                      // 
  import assignSignerInfo from './assignSignerInfo';                              // 
  import commonToggle from './commonToggle';                                      // 
  import assignAlertDialog from './assignAlertDialog';                            // /
  import commonSelector from './commonSelector';                                  // 

  const APPROVAL_LIST = [0, 1, 2];                                          // 

  export default {
    name: "assignDialog",
    data() {
      return {
        healthStatus: 0,                                                          // 
        isDisabledHealths: false,                                                 //     truefalse
        familyStatus: 0,                                                          // 
        isDisabledFamily: false,                                                  //     truefalse
        isHandleFamily: true,                                                     // 
        signStatus: 0,                                                            // 
        isDisabledSign: false,                                                    //     truefalse
        activeIndex: 0,                                                           // 
        isEditHomeFile: false,                                                    // /
        fileList: ['', '', ''],       // 3
        JDJGMC: '',                                                               // 
        GDJGMC: '',                                                               // 
        JDRMC: '',                                                                // 
        ZRYSMC: '',                                                               // 
        JKDAID: '',                                                               // ID
        XM: '',                                                                   // 
        isShowReject: false,                                                      // 
        rejectList: [{                                                            // List
          value: 1,
          label: ''
        }, {
          value: 1,
          label: ''
        }, {
          value: '3',
          label: ''
        }
        ],
        rejectReason: '',                                                         // 
        isShowFamilyReject: false,                                                // 
        rejectFamilyReason: '',                                                   // 
        isShowHealthFile: false,                                                  // 
        isShowHomeFile: false,                                                    // 
        familyInfo: {},                                                            // 
        APPROVAL_LIST,
        sign_audit: false,                                                         //  
        PROVINCEDID: '',                                                           // ID
        PROVINCEDNAME: '',                                                         // 
        CITYID: '',                                                                // ID
        CITYNAME: '',                                                              // 
        DISTRICTID: '',                                                            // ID
        DISTRICTNAME: '',                                                          // 
        STREETID: '',                                                              // ID
        STREETNAME: '',                                                            // 
        COMMITTEEID: '',                                                           // ID
        COMMITTEENAME: '',                                                         // 
        XXDZ: ''                                                                   // 
      }
    },
    components: {
      assignBaseInfo,
      commonTextInput,
      commonAreaSelector,
      assignMember,
      assignSigner,
      assignSignerInfo,
      commonToggle,
      assignAlertDialog,
      commonSelector
    },
    props: {
      ORDERSTATUS: {default: ''},        // 
      JTID: {default: ''},          // id
      QYID: {default: ''},          // id
      toggleMainContent: {},        // 
    },
    created() {
      //  healthStatusisDisabledHealths/ familyStatus, isDisabledFamily /
      const {ORDERSTATUS} = this;
      this.signStatus = ORDERSTATUS;
      let authObj = JSON.parse(this.$localStore.getItem('authority'));
      // 
      let flag = authObj['sign_audit'];
      this.sign_audit = flag;
      this.isDisabledHealths = !flag;
      this.isDisabledFamily = !flag;
      this.isDisabledSign = !flag;
      this.initData();
    },
    methods: {
      /**
       * /
       */
      changeEditState(){
        const {JTID, XM, JKDAID} = this;
        this.isEditHomeFile = !this.isEditHomeFile;
        if(this.isEditHomeFile){
          this.$post('family/sign/order/updateSignFamily',{JTID, XM, JKDAID, }).then(rsp=>{

          }, rej=>{
            if(rej.data.errcode === 460){
              this.$message.error(rej.data.datas[0].message);
            }else{
              this.$message.error(rej.data.errmsg);
            }}
          )
        }
      },
      /**
       * signStatus
       * @param val type:number
       */
      setSignStatus(val){
        this.signStatus = val;
      },
      /**
       * toggle
       * @param index
       */
      toggleFile(index) {
        this.activeIndex = index;
      },
      /**
       * 
       */
      closeContent() {
        this.toggleMainContent(-1)
      },
      /**
       * 
       * @param val type:Number 1: 2:
       */
      toggleHealth(val) {
        this.$post('family/sign/order/auditHealths', {
          'QYID': this.QYID,
          'AUDITSTATUS': val,
          'REFUSEREASON': val === 1 ? '' : this.rejectReason
        }).then(rsp => {
          // 
          this.healthStatus = val;
          // 
          this.isDisabledHealths = true;
          this.toggleHealthDialog(-1);
          this.initData()
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       * @param fag type:string
       **/
      toggleRejctDialog(fag) {
        if (fag === -1) {
          this.isShowReject = false;
        } else {
          this.isShowReject = !this.isShowReject;
        }
      },
      /**
       * 
       */
      confirmRefuseHealths() {
        this.toggleHealth(2);
      },
      /**
       * 
       */
      confirmEstablishHealths() {
        this.toggleHealth(1)
      },
      /**
       * 
       * @param val type:Number 1: 2:
       */
      toggleFamily(val) {
        this.$post('family/sign/order/auditFamily', {
          'QYID': this.QYID,
          'AUDITSTATUS': val,
          'REFUSEREASON': val === 1 ? '' : this.rejectFamilyReason
        }).then(rsp => {
          // 
          this.familyStatus = val;
          // 
          this.isDisabledFamily = true;
          this.toggleHomeDialog(-1);
          this.initData()
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       * @param fag type:string
       **/
      toggleRejctFamilyDialog(fag) {
        if (fag === -1) {
          this.isShowFamilyReject = false;
        } else {
          this.isShowFamilyReject = !this.isShowFamilyReject;
        }
      },
      /**
       * 
       */
      confirmRefuseFamily() {
        this.toggleFamily(2)
      },
      /**
       * 
       */
      confirmEstablishFamily() {
        this.toggleFamily(1)
      },
      /**
       * 
       * @param obj  type: Object {val: 12, res: '', callback: }
       */
      toggleSign(obj) {
        this.$post('family/sign/order/auditOrder', {
          'QYID': this.QYID,
          'AUDITSTATUS': obj.val,
          'REFUSEREASON': obj.val === 1 ? '' : obj.res
        }).then(rsp => {
          // 
          this.signStatus = obj.val;
          // 
          this.isDisabledSign = true;
          obj.callback();
          this.initData()
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       * @param fag type:string
       **/
      toggleHealthDialog(fag) {
        if (fag === -1) {
          this.isShowHealthFile = false;
        } else {
          this.isShowHealthFile = !this.isShowHealthFile;
        }
      },
      /**
       * 
       * @param fag type:string
       **/
      toggleHomeDialog(fag) {
        if (fag === -1) {
          this.isShowHomeFile = false;
        } else {
          this.isShowHomeFile = !this.isShowHomeFile;
        }
      },
      /**
       * 
       */
      initData(){
        const {JTID, ORDERSTATUS, QYID} = this;
        this.$post('family/sign/order/getOrderDetails', {
          JTID, QYID
        }).then(rsp => {
          this.familyInfo = rsp.data;
          const {JDJGMC, GDJGMC, JDRMC, ZRYSMC, PROVINCEDID, CITYID, DISTRICTID, STREETID,
            COMMITTEEID, XXDZ, FAMILYRECORDAUDITED, MEMBERRECORDAUDITED, PROVINCEDNAME, CITYNAME, DISTRICTNAME, STREETNAME,
            COMMITTEENAME, JKDAID, XM} = rsp.data;
          this.JDJGMC = JDJGMC;
          this.GDJGMC = GDJGMC;
          this.JDRMC = JDRMC;
          this.ZRYSMC = ZRYSMC;
          this.PROVINCEDID = PROVINCEDID;
          this.PROVINCEDNAME = PROVINCEDNAME;
          this.CITYID = CITYID;
          this.CITYNAME = CITYNAME;
          this.DISTRICTID = DISTRICTID;
          this.DISTRICTNAME = DISTRICTNAME;
          this.STREETID = STREETID;
          this.STREETNAME = STREETNAME;
          this.COMMITTEEID = COMMITTEEID;
          this.COMMITTEENAME = COMMITTEENAME;
          this.XXDZ = XXDZ;
          this.healthStatus = MEMBERRECORDAUDITED;
          this.familyStatus = FAMILYRECORDAUDITED;
          this.JKDAID = JKDAID;
          this.XM = XM;
          if(MEMBERRECORDAUDITED === 0){
            this.activeIndex = 0;
          }else if(FAMILYRECORDAUDITED === 0){
            this.activeIndex = 1;
          }else if(ORDERSTATUS === 0){
            this.activeIndex = 2;
          }else{
            this.activeIndex = 0;
          }
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      }
    }
  }
</script>

<style lang="scss">
  @media screen and (max-width: 1300px){
    .createHomeFile{
      .baseInfo{
        .commonTextInput .commonTextInputContent .inputType{
          width: 175px;
        }
      }
      .commonAreaSelector .address{
        .commonTextInput .commonTextInputContent .inputType{
          width: 210px;
        }
      }
     .commonAreaSelector .areaSelectorTitle{
        width: 90px;
       text-align: right;
      }
    }
  }
  $border: 1px solid #DDDDDD;
  .assignDialog {
    width: 1354px;
    border-radius: 4px;

    .established {
      color: #4486FF;
      line-height: 30px;
    }

    .refused {
      color: #F65860;
      line-height: 30px;
    }

    .assignDialogTitle {
      width: 100%;
      font-size: 18px;
      color: #ffffff;
      line-height: 40px;
      text-align: center;
      background-color: #4486FF;
      position: relative;

      .closeIcon {
        font-size: 24px;
        cursor: pointer;
        color: #ffffff;
        position: absolute;
        right: 14px;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    .assignDialogContent {
      background-color: #ffffff;
      padding: 15px 60px;

      .contentTitle {
        line-height: 44px;
        font-size: 16px;
        border-bottom: $border;
        text-align: left;
      }

      .fileList {
        border-bottom: $border;

        .file {
          padding: 17px 25px;
          font-size: 16px;
          font-weight: bold;

          &.active {
            color: #4486FF;
            border-bottom: 1px solid #4486FF;
          }
        }
      }

      .fileName {
        padding-top: 28px;
        padding-bottom: 20px;
        text-align: left;
        font-weight: bold;
        font-size: 18px;
      }

      .createNewFile {
        .btns {
          padding-top: 10px;
          text-align: left;

          .major {
            margin-right: 20px !important;
            background-color: #4486ff;
          }
          .refuse{
            background-color: #f65860;
          }

          .el-button {
            padding: 0 0;
            margin: 0 0;
            width: 86px;
            height: 30px;
            line-height: 30px;
          }
        }
      }

      .assignBaseInfo {
        border-top: 1px solid #DDDDDD;
      }

      .createHomeFile {
        .fileName {
          &.memberTitle {
            padding-top: 0;
          }
        }
        .baseInfo {
          display: flex;

          > div {
            margin-right: 40px;

            /*&:nth-child(2) {*/
              /*margin-right: 50px;*/
            /*}*/

            &:nth-last-child(1) {
              margin-right: 0;
            }
          }
        }

        .members {
          display: flex;
          flex-wrap: wrap;

          > div {
            margin-right: 40px;
            margin-bottom: 30px;

            &:nth-child(4n) {
              margin-right: 0;
            }
          }
        }

        .btns {
          text-align: left;

          .el-button {
            width: 86px;
            height: 30px;
            line-height: 30px;
            font-size: 16px;
            margin: 0 0;
            padding: 0 0;
          }

          .major {
            margin-right: 20px;
            background-color: #4486ff;
          }
          .refuse{
            background-color: #f65860;
          }
        }
      }
    }
  }
</style>



