<template>
  <div class="assignBaseInfo" :class="{borderBottom: isShowBorder}">
    <div class="assignBaseHeader">
      <p class="assignBaseInfoTitle"></p>
      <!--<el-button type="primary" class="major" v-if="!isDisabled" @click.prevent.stop="changeEditState">{{isEditInfo ?-->
        <!--'' : ''}}-->
      <!--</el-button>-->
    </div>
    <!--  -->
    <div class="assignBaseInfoInput clearfix">
      <div class="col-1 item fl">
        <commonTextInput v-model="JDJGMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="YHZGXMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="XB" :text="''" :isDisabled="true" :placeholderText="''"></commonTextInput>
        <commonTextInput v-model="HYZKMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
      </div>
      <div class="col-2 item fl">
        <commonTextInput v-model="GDJGMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="XM" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="BRDHHM" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="ZYMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
      </div>
      <div class="col-3 item fl">
        <commonTextInput v-model="JDRMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="SFZJH" :text="''" :isDisabled="isEditInfo || isDisabled" :errorTips="''"
                         :errorState="SFZJHState"></commonTextInput>
        <commonTextInput v-model="MZMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="signName" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
      </div>
      <div class="col-4 item fl">
        <commonTextInput v-model="ZRYSMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="bornDate" :text="''" :isDisabled="true" :placeholderText="''"></commonTextInput>
        <commonTextInput v-model="ABOMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="DAZGZTDMMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
      </div>
    </div>
    <!--  -->

    <!--  -->
    <div class="contentAddress">
      <commonAreaSelector
        :text="''"
        :province.sync="isEditInfo || isDisabled ? HJDZPROVINCEDNAME : HJDZPROVINCEDID"
        :city.sync="isEditInfo || isDisabled ? HJDZCITYNAME : HJDZCITYID"
        :area.sync="isEditInfo || isDisabled ? HJDZDISTRICTNAME : HJDZDISTRICTID"
        :street.sync="isEditInfo || isDisabled ? HJDZSTREETNAME : HJDZSTREETID"
        :village.sync="isEditInfo || isDisabled ? HJDZCOMMITTEENAME : HJDZCOMMITTEEID"
        :address.sync="HJDZXXDZ"
        :isDisabled="isEditInfo || isDisabled"
      >
      </commonAreaSelector>

      <commonAreaSelector
        :text="''"
        :province.sync="isEditInfo || isDisabled ? XZZPROVINCEDNAME : XZZPROVINCEDID"
        :city.sync="isEditInfo || isDisabled ? XZZCITYNAME : XZZCITYID"
        :area.sync="isEditInfo || isDisabled ? XZZDISTRICTNAME : XZZDISTRICTID"
        :street.sync="isEditInfo || isDisabled ? XZZSTREETNAME : XZZSTREETID"
        :village.sync="isEditInfo || isDisabled ? XZZCOMMITTEENAME : XZZCOMMITTEEID"
        :address.sync="XZZXXDZ"
        :isDisabled="isEditInfo || isDisabled"
      >
      </commonAreaSelector>

      <commonAreaSelector
        :text="''"
        :province.sync="isEditInfo || isDisabled ? XZZPROVINCEDNAME : XZZPROVINCEDID"
        :city.sync="isEditInfo || isDisabled ? XZZCITYNAME : XZZCITYID"
        :area.sync="isEditInfo || isDisabled ? XZZDISTRICTNAME : XZZDISTRICTID"
        :street.sync="isEditInfo || isDisabled ? XZZSTREETNAME : XZZSTREETID"
        :village.sync="isEditInfo || isDisabled ? XZZCOMMITTEENAME : XZZCOMMITTEEID"
        :address.sync="XZZXXDZ"
        :isDisabled="isEditInfo || isDisabled"
      >
      </commonAreaSelector>

    </div>
    <!--  -->

    <!--  -->
    <div class="assignPersonInfo">
      <p class="assignPersonInfoTitle"></p>
      <div class="illnessHistory">
        <commonTextInput v-model="YWGMSMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="BLSLBMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
      </div>
      <commonTextInput v-model="CJQKLBMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
    </div>
    <!--  -->

    <!--  -->
    <div class="assignHomeInfo">
      <p class="assignHomeInfoTitle"></p>
      <div class="assignHomeContent">
        <commonTextInput v-model="JZSFQJBLBMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="JZSMQJBLBMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="JZSXDJMJBLBMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="JZSZNJBLBMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
        <commonTextInput v-model="YCSJBDMMC" :text="''" :isDisabled="isEditInfo || isDisabled"></commonTextInput>
      </div>
    </div>
    <!--  -->

  </div>
</template>

<script>
  import commonTextInput from './commonTextInput';             // 
  import commonAreaSelector from './commonAreaSelector';       // 
  import commonDatePicker from './commonDatePicker';            // 
  import {identityCodeValid} from '@/common/js/util';          // 
  export default {
    name: "assignBaseInfo",
    components: {
      commonTextInput,
      commonAreaSelector,
      commonDatePicker
    },
    data() {
      return {
        isEditInfo: true,             // 
        JKDAID: '',                   // Id
        JDJGMC: '',                   // 
        YHZGXMC: '',                  // 
        XB: '',                       // 
        HYZKMC: '',                   // 
        GDJGMC: '',                   // 
        XM: '',                       // 
        BRDHHM: '',                   // 
        ZYMC: '',                     // 
        JDRMC: '',                    // 
        SFZJH: '',                    // 
        MZMC: '',                     // 
        signName: '',                 // 
        ZRYSMC: '',                   // 
        bornDate: '',                 // 
        ABOMC: '',                    // 
        DAZGZTDMMC: '',               // 
        SFZJHState: false,            // 
        YWGMSMC: '',                  // 
        BLSLBMC: '',                  // 
        pastMedicalHistory: '',       // 
        illnessName: '',              // 
        approvalDate: '',             // 
        clinicHospital: '',           // 
        CJQKLBMC: '',               // 
        JZSFQJBLBMC: '',              // 
        JZSMQJBLBMC: '',              // 
        JZSXDJMJBLBMC: '',            // 
        JZSZNJBLBMC: '',              // 
        YCSJBDMMC: '',                // 
        XZZPROVINCEDID: '',           // ID
        XZZPROVINCEDNAME: '',         // 
        XZZCITYID: '',                // ID
        XZZCITYNAME: '',              // 
        XZZDISTRICTID: '',            // ID
        XZZDISTRICTNAME: '',          // 
        XZZCOMMITTEEID: '',           // ID
        XZZCOMMITTEENAME: '',         // 
        XZZSTREETID: '',              // ID
        XZZSTREETNAME: '',            // 
        XZZXXDZ: '',                  // 
        HJDZPROVINCEDID: '',          // ID
        HJDZPROVINCEDNAME: '',        // 
        HJDZCITYID: '',               // ID
        HJDZCITYNAME: '',             // 
        HJDZDISTRICTID: '',           // ID
        HJDZDISTRICTNAME: '',         // 
        HJDZSTREETID: '',             // ID
        HJDZSTREETNAME: '',           // 
        HJDZCOMMITTEEID: '',          // ID
        HJDZCOMMITTEENAME: '',        // 
        HJDZXXDZ: ''                  // 
      }
    },
    watch: {
      // 
      SFZJH(val) {
        let pass = identityCodeValid(val);
        this.SFZJHState = !pass.pass;
        if (pass.pass) {
          const {age, sex, birthDate} = pass.more;
          this.XB = sex;
          this.bornDate = birthDate;
        }
      }
    },
    props: {
      isShowBorder: {default: true},
      memberInfo: {
        default: () => {
        }
      },      // 
      isDisabled: {default: true}
    },
    created() {
      Object.assign(this, this.memberInfo)
    },
    methods: {
      /**
       * /
       */
      changeEditState() {
        const {JKDAID} = this;
        this.isEditInfo = !this.isEditInfo;
        // 
        if(this.isEditInfo){
          this.$post('family/sign/order/updateSignHealth', {JKDAID, }).then(rsp=>{

          }, rej=>{
            if(rej.data.errcode === 460){
              this.$message.error(rej.data.datas[0].message);
            }else{
              this.$message.error(rej.data.errmsg);
            }}
          )
        }
      }
    }
  }
</script>

<style lang="scss">
  @media screen and (max-width: 1300px){
    // 
    .assignBaseInfo .assignBaseInfoInput .item {
      .commonTextInput, .commonDatePicker, .commonSelector{
        .iconText{
          width: 107px;
        }
        .inputType, .datepicker, .selection{
          width: 170px;
        }
      }
      &.col-3{
        .iconText{
          width: 120px;
        }
      }
      &.col-4{
        .iconText{
          width: 135px;
        }
      }
    }
    // 
    .assignBaseInfo .contentAddress .commonAreaSelector{
        .address{
          .commonTextInput .commonTextInputContent .inputType{
            width: 200px;
          }
        }
      .areaSelectorTitle{
        width: 107px!important;
      }
    }
    // 
    .assignBaseInfo .assignPersonInfo, .assignBaseInfo .assignHomeInfo{
      .iconText{
        min-width: 88px!important;
      }
    }
    .assignBaseInfo .assignHomeInfo .assignHomeContent .commonTextInput:nth-child(4n){
      margin-right: 30px !important;
    }
  }
  .assignBaseInfo {
    &.borderBottom {
      border-bottom: 1px solid #DDDDDD;
    }

    .assignBaseHeader {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-top: 15px;
      padding-bottom: 19px;

      .major {
        width: 80px;
        height: 30px;
        padding: 0 0;
        margin: 0 0;

        span {
          line-height: 30px;
        }
      }

      .assignBaseInfoTitle {
        text-align: left;
        font-weight: bold;
        font-size: 18px;
      }
    }


    .assignBaseInfoInput {
      .col-1 {
        margin-right: 15px;

        .iconText {
          width: 75px;
        }
      }

      .col-2 {
        margin-right: 15px;

        .iconText {
          width: 75px;
        }
      }

      .col-3 {
        margin-right: 14px;

        .iconText {
          width: 90px;
        }
      }

      .col-4 {
        .iconText {
          width: 107px;
        }
      }
    }

    .contentAddress{
      .commonAreaSelector .areaSelectorTitle{
          width: 75px;
          text-align: right;
      }
    }

    .assignPersonInfo {
      .assignPersonInfoTitle {
        padding: 8px 0;
        text-align: left;
        font-weight: bold;
        font-size: 18px;
      }

      .illnessHistory {
        display: flex;

        .commonTextInput {
          margin-right: 46px;
        }

        .iconText {
          width: 65px;
        }
      }

      .assignPersonInfoSubTitle {
        text-indent: 17px;
        text-align: left;
        padding-bottom: 20px;
        font-size: 16px;
      }

      .illnessInfo {
        display: flex;

        .iconText {
          width: 65px;
        }

        > div {
          margin-right: 46px;
        }
      }
    }

    .assignHomeInfo {
      .assignHomeInfoTitle {
        padding: 8px 0;
        text-align: left;
        font-weight: bold;
        font-size: 18px;
      }

      .assignHomeContent {
        display: flex;
        flex-wrap: wrap;

        .commonTextInput {
          margin-right: 30px;

          &:nth-child(2) {
            margin-right: 10px;
          }

          &:nth-child(4n) {
            margin-right: 0;
          }
        }
      }
    }

  }
</style>



