<template>
  <div class="weixinDiggList">
    <div class="flexBox">
      <div>
        <div class="weixinDiggTitle">
          <commonTextInput :text="''" v-model="orgName"></commonTextInput>
          <commonTextInput :text="''" v-model="appName"></commonTextInput>
          <commonSelector :text="''" :options="stateList" v-model="isEnabled"></commonSelector>
          <el-button type="primary" class="major" @click.prevent.stop="searchDiggList"></el-button>
        </div>
        <div class="weixinDiggList">
          <div class="weixinDiggTitle">
            <p class="title"></p>
            <div class="functionBtns">
              <el-button type="success" class="major" v-if="wx_secret_upload" @click.stop.prevent=""></el-button>
              <el-button type="primary" class="major" v-if="wx_secret_add" @click.stop.prevent="openDiggInfo('')"></el-button>
            </div>
          </div>
          <div class="weixinDiggTable">
            <div class="tableHeader">
              <div class="tableCell index"></div>
              <div class="tableCell organization"></div>
              <div class="tableCell commonPackage"></div>
              <div class="tableCell levelPackage"></div>
              <div class="tableCell diyPackage"></div>
              <div class="tableCell handle"></div>
            </div>
            <div class="tableRow" v-for="(item, index) in dataList" :key="index">
              <div class="tableCell index">
                <div>{{index+1}}</div>
              </div>
              <div class="tableCell organization">
                <div>{{item.orgName }}</div>
              </div>
              <div class="tableCell commonPackage">
                <div>{{item.appName}}</div>
              </div>
              <div class="tableCell levelPackage">
                <div>{{item.createTime}}</div>
              </div>
              <div class="tableCell diyPackage">
                <div>{{item.isEnabled ? '' : ''}}</div>
              </div>
              <div class="tableCell handle">
                <el-button type="primary" class="major" v-if="wx_secret_edit" @click.prevent.stop="openDiggInfo(item.wxConfigId)"></el-button>
                <el-button type="primary" class="major" v-if="wx_secret_edit" @click.prevent.stop="disabledApp(item.wxConfigId, !item.isEnabled)">
                  {{item.isEnabled ? '' : ''}}
                </el-button>
                <span v-if="!wx_secret_edit"></span>
              </div>
            </div>
          </div>
          <div class="noneData" v-if="dataList.length === 0">
            
          </div>
        </div>
      </div>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNo"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          layout=" prev, pager, next,total, sizes, jumper"
          background
          :total="totalCount">
        </el-pagination>
      </div>
    </div>
    <editWeixinDiggList
      :isShow="isShowWeixinInfo"
      :toggleMainContent="toggleMainContent"
      :wxConfigId="wxConfigId"
    >

    </editWeixinDiggList>
  </div>
</template>

<script>
  import commonTextInput from '@/components/commonTextInput';
  import editWeixinDiggList from '@/components/editWeixinDiggList';
  import commonSelector from '@/components/commonSelector';

  export default {
    name: "weixinDiggList",
    components: {
      commonTextInput,
      editWeixinDiggList,
      commonSelector
    },
    data() {
      return {
        wxConfigId: '',                   // Id
        orgName: '',                      // 
        appName: '',                      // 
        isEnabled: '',                    // 
        pageNo: 1,                        // 
        pageSize: 10,                     // 
        totalCount: 0,                    // 
        dataList: [],                     // List
        stateList: [
          {label: '', value: ''},
          {
            label: '',
            value: true
          },
          {
            label: '',
            value: false
          }
        ],
        isShowWeixinInfo: false,
        isShowWeixinDiggDialog: false,
        wx_secret_add: false,             // 
        wx_secret_edit: false,            // 
        wx_secret_upload: false           // 
      }
    },
    methods: {
      /**
       * 
       * @param fag
       */
      toggleMainContent(fag) {
        if (fag === -1) {
          this.isShowWeixinInfo = false;
        } else {
          this.isShowWeixinInfo = !this.isShowWeixinInfo;
        }
        if(!this.isShowWeixinInfo){
          this.searchDiggList()
        }
      },
      /**
       * /
       * @param wxConfigId type: string
       */
      openDiggInfo(wxConfigId) {
        this.wxConfigId = wxConfigId;
        this.toggleMainContent();
      },
      /**
       * 
       * @param fag
       */
      toggleInfoDialog(fag) {
        if (fag === -1) {
          this.isShowWeixinDiggDialog = false;
        } else {
          this.isShowWeixinDiggDialog = !this.isShowWeixinDiggDialog;
        }
      },
      /**
       * 
       * @param wxConfigId    type:string
       * @param isEnabled     type:boolean
       */
      disabledApp(wxConfigId, isEnabled) {
        this.$post('wx/secret/info/setEnable', {
          wxConfigId,
          isEnabled
        }).then(rsp => {
          this.searchDiggList();
          // console.log('');
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * List
       */
      searchDiggList() {
        const {orgName, appName, isEnabled, pageNo, pageSize} = this;
        this.$post('wx/secret/info/getPage', {
          orgName, appName, isEnabled, pageNo, pageSize
        }).then(rsp => {
          this.dataList = rsp.datas;
          this.totalCount = rsp.total;
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }});
      },
      /**
       * pageSize
       * @param val
       */
      handleSizeChange(val){
        this.pageSize = val;
        this.searchDiggList();
      },
      /**
       * 
       */
      handleCurrentChange(val){
        this.pageNo = val;
        this.searchDiggList();
      }
    },
    created() {
      this.searchDiggList();
      let authObj = JSON.parse(this.$localStore.getItem('authority'));
      // 
      if(!!authObj){
        this.wx_secret_add = authObj['wx_secret_add'];
        this.wx_secret_edit = authObj['wx_secret_edit'];
        this.wx_secret_upload = authObj['wx_secret_upload'];
      }
    }
  }
</script>

<style lang="scss">
  @import '../style/weixinDiggList.scss';
</style>



