<template>
  <div class="commonAreaSelector clearfix">
    <i class="fl areaSelectorTitle">{{text}}</i>
    <div class="province fl">
      <el-select v-model="provinceId" :disabled="isDisabled" class="selection fl" :placeholder="isDisabled ? '' : ''">
        <el-option
          v-if="!isDisabled"
          v-for="item in provinceList"
          :key="item.unitId"
          :label="item.name"
          :value="item.unitId">
        </el-option>
        <el-option v-if="isDisabled" :label="provinceId" :value="provinceId" :placeholder="isDisabled ? '' : ''"></el-option>
      </el-select>
      <i class="fl iconText"></i>
    </div>
    <div class="city fl">
      <el-select v-model="cityId" :disabled="isDisabled" class="selection fl" :placeholder="isDisabled ? '' : ''">
        <el-option
          v-if="!isDisabled"
          v-for="item in cityList"
          :key="item.unitId"
          :label="item.name"
          :value="item.unitId">
        </el-option>
        <el-option v-if="isDisabled" :label="cityId" :value="cityId" :placeholder="isDisabled ? '' : ''"></el-option>
      </el-select>
      <i class="fl iconText"></i>
    </div>
    <div class="area fl">
      <el-select v-model="areaId" :disabled="isDisabled" class="selection fl" :placeholder="isDisabled ? '' : ''">
        <el-option
          v-if="!isDisabled"
          v-for="item in areaList"
          :key="item.unitId"
          :label="item.name"
          :value="item.unitId">
        </el-option>
        <el-option v-if="isDisabled" :label="areaId" :value="areaId" :placeholder="isDisabled ? '' : ''"></el-option>
      </el-select>
      <i class="fl iconText"></i>
    </div>
    <div class="street fl">
      <!--<commonTextInput v-model="street" :isDisabled="isDisabled" :text="''"></commonTextInput>-->
      <i class="fl iconText"></i>
      <el-select v-model="streetId" :disabled="isDisabled" class="selection fl" :placeholder="isDisabled ? '' : ''">
        <el-option
          v-if="!isDisabled"
          v-for="item in streetList"
          :key="item.unitId"
          :label="item.name"
          :value="item.unitId">
        </el-option>
        <el-option v-if="isDisabled" :label="streetId" :value="streetId" :placeholder="isDisabled ? '' : ''"></el-option>
      </el-select>
    </div>
    <div class="village fl">
      <!--<commonTextInput v-model="village" :isDisabled="isDisabled" :text="''"></commonTextInput>-->
      <i class="fl iconText"></i>
      <el-select v-model="villageId" :disabled="isDisabled" class="selection fl" :placeholder="isDisabled ? '' : ''">
        <el-option
          v-if="!isDisabled"
          v-for="item in villageList"
          :key="item.unitId"
          :label="item.name"
          :value="item.unitId">
        </el-option>
        <el-option v-if="isDisabled" :label="villageId" :value="villageId" :placeholder="isDisabled ? '' : ''"></el-option>
      </el-select>
    </div>
    <div class="address fl">
      <commonTextInput v-model="subAddress" :isDisabled="isDisabled" :text="''" ></commonTextInput>
    </div>
  </div>
</template>

<script>
  import commonTextInput from './commonTextInput';

  export default {
    name: "commonAreaSelector",
    components: {
      commonTextInput
    },
    data() {
      return {
        provinceId: this.province,
        provinceList: [                 // 
        ],
        cityId: this.city,
        cityList: [                     // 
        ],
        areaId: this.area,
        areaList: [                     // 
        ],
        streetId: this.street,
        streetList: [],                  // List
        villageId: this.village,
        villageList: [],                // List
        subAddress: this.address        // 
      }
    },
    props: {
      text: {default: ''},          // 
      province: {default: ''},      // 
      city: {default: ''},          // 
      area: {default: ''},          // 
      street: {default: ''},        // 
      village: {default: ''},       // 
      address: {default: ''},       // 
      isDisabled: {default: false}, // 
    },
    watch: {
      /**
       * ID
       */
      province(val) {
        this.provinceId = val;
      },
      /**
       * 
       */
      provinceId(val) {
        if (!this.isDisabled) {
          this.getRegion(val, (data) => {
            this.cityList = data;
          });
          // ID
          this.$emit('update:province', val);
        }
      },
      /**
       * ID
       */
      city(val) {
        this.cityId = val;
      },
      /**
       * 
       */
      cityId(val) {
        if (!this.isDisabled){
          this.getRegion(val, (data) => {
            this.areaList = data;
          });
          // ID
          this.$emit('update:city', val);
        }
      },
      /**
       * ID
       */
      area(val) {
        this.areaId = val;
      },
      /**
       * 
       */
      areaId(val) {
        if (!this.isDisabled){
          this.getRegion(val, (data) => {
            this.streetList = data;
          });
          // ID
          this.$emit('update:area', val);
        }
      },
      /**
       * ID
       */
      street(val) {
        this.streetId = val;
      },
      /**
       * 
       */
      streetId(val) {
        if (!this.isDisabled){
          this.getRegion(val, (data) => {
            this.villageList = data;
          });
          // ID
          this.$emit('update:street', val);
        }
      },
      /**
       * ID
       */
      village(val) {
        this.villageId = val;
      },
      /**
       * ID
       */
      villageId(val) {
        this.$emit('update:village', val);
      },
      /**
       * 
       */
      address(val) {
        this.subAddress = val;
      },
      /**
       * 
       */
      subAddress(val) {
        this.$emit('update:address', val);
      }
    },
    methods: {
      /**
       * 
       * @param parentId
       * @param callback
       */
      getRegion(parentId, callback) {
        this.$post('region/getChild', {parentId}).then(rsp => {
          callback(rsp.datas)
        }, rej => {
          if (rej.data.errcode === 460) {
            this.$message.error(rej.data.datas[0].message);
          } else {
            this.$message.error(rej.data.errmsg);
          }
        })
      }
    },
    created() {
      const {province, city, area, street, village, isDisabled} = this;
      // 
      if(!isDisabled){
        this.getRegion('', (data) => {
          this.provinceList = data;
        });
      }


      if (province && !isDisabled) {
        // 
        this.getRegion(province, (data) => {
          this.cityList = data;
        });
      }
      if (city && !isDisabled) {
        // 
        this.getRegion(city, (data) => {
          this.areaList = data;
        });
      }
      if (area && !isDisabled) {
        // 
        this.getRegion(area, (data) => {
          this.streetList = data;
        });
      }
      if (street && !isDisabled) {
        // 
        this.getRegion(street, (data) => {
          this.villageList = data;
        });
      }
    }
  }
</script>

<style lang="scss">
  .commonAreaSelector {
    font-size: 16px;
    line-height: 30px;

    .province, .city, .area {
      margin-left: 6px;

      .el-select, .el-input, .el-input__inner {
        width: 82px;
        height: 30px;
      }

      .iconText {
        margin-left: 8px;
      }
    }

    .selection {
      width: 80px;
      height: 30px;

      input {
        width: 75px;
        height: 30px;
        font-size: 16px;
        &::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
          /* WebKit browsers */
          color: #BABABA;
        }

        &:-moz-placeholder, textarea:-moz-placeholder {
          /* Mozilla Firefox 4 to 18 */
          color: #BABABA;
        }

        &::-moz-placeholder, textarea::-moz-placeholder {
          /* Mozilla Firefox 19+ */
          color: #BABABA;
        }

        &:-ms-input-placeholder, textarea:-ms-input-placeholder {
          /* Internet Explorer 10+ */
          color: #BABABA;
        }
      }

      .el-input__icon {
        line-height: 30px;
      }

      .el-input__inner {
        padding-left: 4px;
      }
    }

    .street, .village, .address {
      margin-left: 6px;
    }

    .street, .village {
      .el-select {
        margin-left: 8px;
      }

      .el-select, .el-input, .el-input__inner {
        width: 140px;
        height: 30px;
      }
    }

    .address {
      .commonTextInput {
        .commonTextInputContent {
          .inputType {
            width: 300px;
            height: 30px;
          }
        }
      }

    }
  }
</style>



