import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

// store
let defStore = new Vuex.Store({
  state: {
    orderPage: '0',                          // 
    isLogin: false                           // 
  },
  mutations: {
    /**
     * state
     * @param state
     * @param data
     */
    updateState(state, data) {
      for (let key in data) {
        if (data.hasOwnProperty(key)) {
          state[key] = data[key];
        }
      }
    },
    /**
     * 
     * @param state
     * @param flag
     */
    updateLoginState(state, flag){
      state.isLogin = flag;
    }
  },
  actions: {
    /**
     * page
     * @param commit
     * @param orderPage  page
     */
    setDefaultOrderPage({commit}, orderPage) {
      if (orderPage) commit('updateState', {orderPage})
    },
    /**
     * Login
     * @param commit
     * @param flag
     */
    setIsLoginState({commit}, flag){
      commit('updateLoginState', flag)
    }
  }
});

export default defStore;



