let Timer = require('timer.js');
// import Timer from 'Timer';

// keytimer
let _timerMach = {};
let _maxDuration = 240 * 60;    // 120

let _fn = {
    /**
     * callback
     * @param ops   
     */
    setTimer(ops) {
        let defOps = {
            key: '',            // key
            startTime: 0,       // 
            duration: -1,       // 
            callback: ()=>{}    // 
        };
        ops = Object.assign({}, defOps, ops || {});
        let {key} = ops;
        if(key !== ''){
            if(_timerMach[key]){
                this.clearTimer(key);
            }
            let timer = new Timer({
                ontick: function(duration){
                    if(ops.duration !== -1){
                        ops.callback(Math.floor(duration/1000));
                    }else{
                        ops.callback(+ops.startTime + Math.floor((_maxDuration*1000 - duration)/1000));
                    }
                }
            });

            _timerMach[key] = {
                timer: timer.start(ops.duration === -1 ? _maxDuration : ops.duration),
                options: ops
            };
            return timer;
        }else{
            return null;
        }
    },
    /**
     * 
     * @param key   key
     */
    clearTimer(key) {
        if(_timerMach[key]){
            let timer = _timerMach[key].timer;
            timer.stop();
            _timerMach[key] = null;
        }
    },
    /**
     * 
     */
    clearAllTimer() {
        for(let key in _timerMach){
            this.clearTimer(key);
        }
    },
    /**
     * 
     * @param ops   
     */
    resetTimer(ops) {
        let key = (ops || {}).key;
        if(key && _timerMach[key]){
            let { options } = _timerMach[key];
            this.clearTimer(key);
            options = Object.assign(options, ops);
            return this.setTimer(options);
        }else{
            return null;
        }
    }
};

export default _fn



