<template>
  <div class="addNewTeam">
    <!--  -->
    <div class="addNewTeamTitle">
      <span>{{teamId ? '' : ''}}</span>
      <div class="closeIcon" @click.stop.prevent="closeContent">&times;</div>
    </div>
    <!--  -->

    <!--  -->
    <div class="addNewTeamContent">
      <commonAutocomplete
        :text="''"
        v-model="teamId ? mechanismName : mechanism"
        :isDisabled="!!teamId"
        v-if="isShowOrg"
        :filterState="filterOrgs"
      >
      </commonAutocomplete>
      <commonTextInput :text="''" v-model="teamName"></commonTextInput>
      <commonSelector
        :text="''"
        v-model="ISENABLED"
        :options="ISENABLEDList"
        :isDisabled="!teamId"
      >
      </commonSelector>
      <div class="avatarUploader">
        <i class="iconText"></i>
        <el-upload
          class="avatar-uploader"
          action="string"
          :headers="headers"
          :with-credentials="true"
          :show-file-list="false"
          :http-request="uploadImage"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="imgId" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
        <i class="tips">:640&times;640</i>
      </div>
      <div class="teamIntro clearfix">
        <div class="intro fl"></div>
        <el-input
          class="textArea fl"
          type="textarea"
          placeholder=""
          resize="none"
          v-model="teamTextarea"
          maxlength="300"
          show-word-limit
        >
        </el-input>
      </div>
      <div class="teamIntro clearfix">
        <div class="intro fl"></div>
        <el-input
          class="textArea fl"
          type="textarea"
          placeholder=""
          resize="none"
          v-model="teamSkillTextarea"
          maxlength="300"
          show-word-limit
        >
        </el-input>
      </div>

      <!--  -->
      <div class="assignDialogFooter">
        <el-button type="primary" class="major" @click.stop.prevent="setEstablish"></el-button>
        <el-button type="info" class="info" @click.stop.prevent="closeContent"></el-button>
      </div>
      <!--  -->
    </div>
    <!--  -->

    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowTeam"
      :toggleContent="toggleTeamDialog"
      :confirmDialog="addNewMember"
    >
      <div class="paddingBox">?</div>
    </assignAlertDialog>
    <!--  -->

  </div>
</template>

<script>
  import commonTextInput from './commonTextInput';
  import commonSelector from './commonSelector';
  import assignAlertDialog from '@/components/assignAlertDialog';                  // /
  import commonAutocomplete from '@/components/commonAutocomplete';

  export default {
    name: "addNewTeam",
    data() {
      return {
        mechanism: '',                      // 
        mechanismName: '',                  // 
        isShowOrg: true,                    // 
        mechanismList: [],                  // List
        teamName: '',                       // 
        ISENABLEDList: [                    // Lsit
          {
            label: '',
            value: true
          },
          {
            label: '',
            value: false
          }
        ],
        ISENABLED: true,                    // 
        teamTextarea: '',                   // 
        teamSkillTextarea: '',              // 
        imageUrl: '',                       // 
        imgId: '',                          // Id
        isShowTeam: false,                  // 
        createId: '',                       // Id
        requestUrl: this.$uploadUrl,
        headers: {                            // 
          "Content-Type": 'multipart/form-data'
        }
      }
    },
    components: {
      commonTextInput,
      commonSelector,
      assignAlertDialog,
      commonAutocomplete
    },
    props: {
      teamId: {default: ''},            // Id
      toggleContent: {},                // 
    },
    methods: {
      /**
       * 
       **/
      closeContent() {
        this.toggleContent(-1);
      },
      /**
       * 
       * @param file
       * @returns {boolean}
       */
      beforeAvatarUpload(file) {
        const isLt5M = file.size / 1024 / 1024 < 5;

        if (!isLt5M) {
          this.$message.error(' 5MB!');
        }
        return isLt5M;
      },
      /**
       * 
       * @param fag
       */
      toggleTeamDialog(fag) {
        if (fag === -1) {
          this.isShowTeam = false;
        } else {
          this.isShowTeam = !this.isShowTeam;
        }
      },
      /**
       * 
       */
      setEstablish() {
        const {mechanism, teamName, teamTextarea, teamSkillTextarea, imgId, ISENABLED} = this;
        if (this.teamId) {
          // 
          this.$post('family/teamAndMember/updateTeam', {
            TDID: this.teamId,
            ORGID: mechanism,
            TDMC: teamName,
            TDMS: teamTextarea,
            TDSC: teamSkillTextarea,
            IMGFILEID: imgId,
            ISENABLED
          }).then(rsp => {
            // 
            this.$emit('regetSource');
            this.toggleContent(-1);
          }, rej => {
            if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }});
        } else {
          // 
          this.$post('family/teamAndMember/addTeam', {
            ORGID: mechanism,
            TDMC: teamName,
            TDMS: teamTextarea,
            TDSC: teamSkillTextarea,
            IMGFILEID: imgId
          }).then(rsp => {
            this.createId = rsp.data.TDID;
            this.$emit('regetSource');
            this.toggleTeamDialog();
            this.toggleContent(-1);
          }, rej => {
            if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
        }
      },
      /**
       * 
       */
      addNewMember() {
        // 
        this.$emit('openMember', this.createId, this.mechanism);
        this.toggleContent();
      },
      /**
       * 
       * @param str type:string 
       * @param res type:Function promise
       * @param rej type:Function promise
       */
      filterOrgs(str, res, rej) {
        // 
        this.$post('organ/getPage', {
          orgName: str,
          pageNo: 1,
          pageSize: 10
        }).then(rsp => {
          let result = rsp.datas.map(item => {
            item.id = item.orgId;
            item.value = item.orgName;
            return item
          });
          res(result)
        }, reject => {
          this.$message.error(reject.data.errmsg);
          rej([])
        });
      },
      /**
       * 
       */
      uploadImage(param) {
        const formData = new FormData();
        formData.append('content', param.file);
        this.$post(this.requestUrl, formData).then(rsp => {
          if (rsp.errcode !== 0) {
            this.$message.error(rsp.errmsg);
          } else {
            this.$message.success(rsp.errmsg);
            this.imgId = rsp.data.id;
            this.imageUrl = this.$downloadUrl + rsp.data.id;
          }
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       * @param file
       * @returns {boolean}
       */
      beforeAvatarUpload(file) {
        const isTp = file.type.indexOf('image') !== -1;

        if (!isTp) {
          this.$message.error('!');
        }

        const isLt5M = file.size / 1024 / 1024 < 5;

        if (!isLt5M) {
          this.$message.error(' 5MB!');
        }
        return isLt5M && isTp
      },
    },
    created() {
      // 
      if (this.teamId) {
        this.$post('family/teamAndMember/getByTDID', {
          TDID: this.teamId
        }).then(rsp => {
          const {ORGID, ORGNAME, TDMC, TDMS, TDSC, IMGFILEID, ISENABLED} = rsp.data;
          this.mechanism = ORGID;
          this.mechanismName = ORGNAME;
          this.teamName = TDMC;
          this.teamTextarea = TDMS;
          this.teamSkillTextarea = TDSC;
          this.imgId = IMGFILEID;
          this.ISENABLED = ISENABLED;
          this.imageUrl = this.$downloadUrl + IMGFILEID;
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      }

      // 
      let roleType = JSON.parse(this.$localStore.getItem('role')).roleType;
      if (!this.teamId && roleType === 2) {
        this.isShowOrg = false;
      }
    }
  }
</script>

<style lang="scss">
  @media screen and (max-width: 1300px){
      .addNewTeam .addNewTeamContent{
        .intro,.iconText{
          width: 90px!important;
        }
        .assignDialogFooter{
          padding-left: 90px!important;
        }
      }
  }
  .addNewTeam {
    font-size: 16px;

    .paddingBox {
      padding: 30px 0;
    }

    .iconText {
      width: 75px;
      text-align: right;
    }

    .addNewTeamTitle {
      width: 100%;
      color: #ffffff;
      font-size: 18px;
      line-height: 40px;
      text-align: center;
      background-color: #4486FF;
      position: relative;

      .closeIcon {
        font-size: 24px;
        cursor: pointer;
        color: #ffffff;
        position: absolute;
        right: 14px;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    .addNewTeamContent {
      padding: 40px 60px;

      .avatarUploader {
        display: flex;
        align-items: center;
        margin-bottom: 23px;

        .avatar-uploader .el-upload {
          border: 2Px solid #DDDDDD;
          border-radius: 50%;
          cursor: pointer;
          position: relative;
          overflow: hidden;
        }

        .avatar-uploader .el-upload:hover {
          border-color: #409EFF;
        }

        .avatar-uploader {
          margin-left: 10px;
        }

        .avatar-uploader-icon {
          font-size: 28px;
          color: #8c939d;
          width: 60px;
          height: 60px;
          line-height: 60px;
          text-align: center;
        }

        .avatar {
          width: 60px;
          height: 60px;
          display: block;
        }

        .tips {
          color: #F65860;
          font-size: 12px;
          margin-left: 10px;
        }
      }

      .teamIntro {
        margin-bottom: 30px;
        width: 840px;

        .intro {
          width: 75px;
          margin-right: 10px;
          text-align: center;
          vertical-align: middle;

          &.center {
            margin-top: 72px;
          }
        }

        .el-textarea__inner, .textArea {
          width: 740px;
          height: 160px;

          .el-input__count {
            color: #F65860;
          }
        }

        .teamMember {
          display: flex;
          flex-wrap: wrap;

          > div {
            margin-right: 42px;

            &:nth-child(4n) {
              margin-right: 0;
            }
          }

          .member {
            width: 160px;
            height: 160px;
            border: 1px solid #DDDDDD;
            border-radius: 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            line-height: 23px;
            font-size: 16px;
            position: relative;

            .icon {
              cursor: pointer;
              position: absolute;
              top: -14px;
              right: -14px;
              width: 28px;
              height: 28px;
              border-radius: 50%;
              background: url("../assets/btn_delete.png") no-repeat center center;
              background-size: cover;
              vertical-align: middle;
              text-align: center;
              color: #ffffff;
              font-size: 28px;
            }

            .avatar {
              width: 60px;
              height: 60px;
              margin-bottom: 6px;
            }
          }

          .addTeamMember {
            cursor: pointer;
            width: 160px;
            height: 160px;
            background: url("../assets/btn_add_big.png") no-repeat center center;
            background-size: cover;
          }
        }
      }

    }

    .assignDialogFooter {
      margin: 0 auto;
      padding-left:75px;
      text-indent: 0;

      .el-button {
        margin: 0 10px;
      }
    }

  }
</style>



