<template>
  <div class="weixinDiggInfo">
    <div class="flexBox">
      <div>
        <div class="weixinDiggSearch">
          <commonTextInput :text="''" v-model="orgName"></commonTextInput>
          <commonTextInput :text="''" v-model="appName"></commonTextInput>
          <commonTextInput :text="''" v-model="title"></commonTextInput>
          <commonSelector :text="''" v-model="category" :options="categoryList"></commonSelector>
        </div>
        <div class="weixinDiggTime clearfix">
          <div class="filterTime fl">
            <commonDatePicker :text="''" :placeholderText="''" :pickerOptions="beginPickerOptions" v-model="beginPubTime"></commonDatePicker>
            <commonDatePicker :text="''" :placeholderText="''" :pickerOptions="endPickerOptions" v-model="endPubTime"></commonDatePicker>
          </div>
          <el-button type="primary" class="major fr" @click.prevent.stop="searchDiggInfo"></el-button>
        </div>
        <div class="weixinDiggContent">
          <div class="personTitle">
            <p class="title"></p>
            <div class="functionBtns">
              <el-button type="primary" class="major" v-if="wx_news_add" @click.stop.prevent="openDiggInfo('')">
              </el-button>
            </div>
          </div>
          <div class="personContent">
            <div class="personTable">
              <div class="tableHeader">
                <div class="tableCell index"></div>
                <div class="tableCell name"></div>
                <div class="tableCell code"></div>
                <div class="tableCell organization"></div>
                <div class="tableCell leader"></div>
                <div class="tableCell count"></div>
                <div class="tableCell state"></div>
                <div class="tableCell provide"></div>
                <div class="tableCell handle"></div>
              </div>
              <div class="filterDate"></div>
              <div class="tableRow" v-for="(item, index) in dataList" :key="index">
                <div class="tableCell">{{index+1}}</div>
                <div class="tableCell">{{item.category === 0 ? '' : item.orgName}}</div>
                <div class="tableCell">{{item.category === 0 ? '' : item.appName}}</div>
                <div class="tableCell">{{item.title}}</div>
                <div class="tableCell">{{item.category}}</div>
                <div class="tableCell">{{item.readingNumber}}</div>
                <div class="tableCell">{{item.approveNumber}}</div>
                <div class="tableCell">{{item.createTime}}</div>
                <div class="tableCell">
                  <el-button type="primary" class="major" v-if="wx_news_edit"
                             @click.prevent.stop="setTopState(item.newsId, !item.isSticky)">{{item.isSticky ? '' :
                    ''}}
                  </el-button>
                  <el-button type="primary" class="major" v-if="wx_news_edit"
                             @click.prevent.stop="openDiggInfo(item.newsId)">
                  </el-button>
                  <el-button type="danger" class="major" v-if="wx_news_delete"
                             @click.prevent.stop="deleteDiggInfo(item.newsId)">
                  </el-button>
                  <span v-if="!wx_news_edit && !wx_news_delete"></span>
                </div>
              </div>
            </div>
            <div class="noneData" v-if="dataList.length === 0">
              
            </div>
          </div>
        </div>
      </div>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNo"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          layout=" prev, pager, next,total, sizes, jumper"
          background
          :total="totalCount">
        </el-pagination>
      </div>
    </div>
    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowRole"
      :toggleContent="toggleRoleDialog"
      :confirmDialog="confirmDelete"
    >
      <div class="paddingBox"></div>
    </assignAlertDialog>
    <!--  -->

    <!-- / -->
    <editWeixinDiggInfo
      :isShow="isShowCommonDialog"
      :toggleMainContent="toggleMainContent"
      :newsId="newsId"
    >

    </editWeixinDiggInfo>
    <!-- / -->
  </div>
</template>

<script>
  import commonTextInput from '@/components/commonTextInput';
  import commonSelector from '@/components/commonSelector';
  import assignAlertDialog from '@/components/assignAlertDialog';                  // /
  import commonDialog from '@/components/commonDialog';                            // 
  import commonDatePicker from '@/components/commonDatePicker';                    // 
  import editWeixinDiggInfo from '@/components/editWeixinDiggInfo';                // 
  export default {
    name: "weixinDiggInfo",
    components: {
      commonTextInput,
      commonSelector,
      assignAlertDialog,
      editWeixinDiggInfo,
      commonDialog,
      commonDatePicker
    },
    data() {
      return {
        isShowRole: false,                              // 
        isShowCommonDialog: false,                      // 
        deleteId: '',                                   // id
        newsId: '',                                     // Id
        dataList: [],                                   // List
        orgName: '',                                    // 
        appName: '',                                    // 
        title: '',                                      // 
        category: '',                                   // 
        beginPubTime: '',                               // 
        beginPickerOptions: {
          disabledDate: (time) => {
            let nowData = new Date();
            if (this.endPubTime) {
              // 
              let approvalDate = new Date(this.endPubTime);
              return time > nowData || time > approvalDate
            } else {
              // 
              return time > nowData
            }
          }
        },
        endPubTime: '',                                 // 
        endPickerOptions: {
          disabledDate: (time) => {
            let nowData = new Date();
            if (this.beginPubTime) {
              // 
              let applyDate = new Date(this.beginPubTime);
              return time > nowData || time < applyDate
            } else {
              // 
              return time > nowData
            }
          }
        },
        categoryList: [                             // List
          {
            label: '',
            value: ''
          },
          {
            label: '',
            value: 0
          },
          {
            label: '',
            value: 1
          },
        ],
        pageNo: 1,
        pageSize: 10,
        totalCount: 0,                                  // 
        wx_news_add: false,                             // 
        wx_news_edit: false,                            // 
        wx_news_delete: false                           // 
      }
    },
    methods: {
      /**
       * 
       * @param fag
       **/
      toggleRoleDialog(fag) {
        if (fag === -1) {
          this.isShowRole = false;
        } else {
          this.isShowRole = !this.isShowRole;
        }
        if(!this.isShowRole){
          this.searchDiggInfo()
        }
      },
      /**
       * 
       * @param fag
       **/
      toggleMainContent(fag) {
        if (fag === -1) {
          this.isShowCommonDialog = false;
        } else {
          this.isShowCommonDialog = !this.isShowCommonDialog;
        }
        if(!this.isShowCommonDialog){
          this.searchDiggInfo()
        }
      },
      /**
       * 
       * @param newsId      Id
       * @param isSticky    
       */
      setTopState(newsId, isSticky) {
        this.$post('news/updateSticky', {
          newsId,
          isSticky
        }).then(rsp => {
          this.searchDiggInfo();
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }});
      },
      /**
       * /
       * @param newsId type: string
       */
      openDiggInfo(newsId) {
        this.newsId = newsId;
        this.toggleMainContent();
      },
      /**
       * 
       */
      searchDiggInfo() {
        const {orgName, appName, title, category, beginPubTime, endPubTime, pageSize, pageNo} = this;
        this.$post('news/getPage', {
          orgName, appName, title, category, beginPubTime, endPubTime, pageSize, pageNo
        }).then(rsp => {
          this.dataList = rsp.datas;
          this.totalCount = rsp.total;
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       * @param newsId type:string
       */
      deleteDiggInfo(newsId) {
        this.deleteId = newsId;
        this.toggleRoleDialog();
      },
      /**
       * 
       */
      confirmDelete() {
        this.$post('news/delete', {
          newsId: this.deleteId
        }).then(rsp => {
          // console.log("");
          this.toggleRoleDialog(-1)
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * pageSize
       * @param val
       */
      handleSizeChange(val) {
        this.pageSize = val;
        this.searchDiggInfo();
      },
      /**
       * 
       */
      handleCurrentChange(val) {
        this.pageNo = val;
        this.searchDiggInfo();
      }
    },
    created() {
      this.searchDiggInfo();
      let authObj = JSON.parse(this.$localStore.getItem('authority'));
      // 
      if (!!authObj) {
        this.wx_news_add = authObj['wx_news_add'];
        this.wx_news_edit = authObj['wx_news_edit'];
        this.wx_news_delete = authObj['wx_news_delete'];
      }
    }
  }
</script>

<style lang="scss">
  @import '../style/weixinDiggInfo.scss';
</style>



