<template>
  <div class="signManage">
    <div class="flexBox">
      <div>
        <!--  -->
        <div class="topSpecial">
          <commonSelector :options="provinceOptions" v-model="provinceId" :text="''"></commonSelector>
          <commonSelector :options="cityOptions" v-model="cityId"></commonSelector>
          <commonSelector :options="areaOptions" v-model="areaId"></commonSelector>
          <commonTextInput v-model="medicalInsVal" :text="''"></commonTextInput>
          <commonTextInput v-model="doctorTeamVal" :text="''"></commonTextInput>
        </div>
        <div class="filterCondition clearfix">
          <div class="item col-1 fl">
            <!--<commonSelector :options="signChannelList" v-model="signChannelVal" :text="''"></commonSelector>-->
            <commonTextInput v-model="doctorName" :text="''"></commonTextInput>
            <!--<commonSelector :options="personCategoryList" v-model="personCategoryVal" :text="''"></commonSelector>-->
            <commonDatePicker v-model="applyDateVal" :text="''" :pickerOptions="applyPickerOptions"></commonDatePicker>
          </div>
          <div class="item col-2 fl">
            <commonTextInput v-model="accountVal" :text="''"></commonTextInput>
            <commonDatePicker v-model="applyEndDateVal" :text="''" :pickerOptions="applyEndPickerOptions"></commonDatePicker>
          </div>
          <div class="item col-3 fl">
            <commonTextInput v-model="identityCardVal" :text="''"></commonTextInput>
            <commonDatePicker v-model="approvalDateVal" :text="''"
                              :pickerOptions="approvalPickerOptions"></commonDatePicker>

          </div>
          <div class="item col-4 fl">
            <commonTextInput v-model="signNameVal" :text="''"></commonTextInput>
            <commonDatePicker v-model="approvalEndDateVal" :text="''"
                              :pickerOptions="approvalEndPickerOptions"></commonDatePicker>
          </div>
          <div class="item col-5 fl">
            <commonTextInput v-model="signIdentityCardVal" :text="''"></commonTextInput>
            <commonSelector :options="stateList" v-model="signSate" :text="''"></commonSelector>
            <div class="btns">
              <el-button type="primary" class="major" @click.stop.prevent="searchSignList"></el-button>
              <!--<el-button type="success" @click.prevent.stop=""></el-button>-->
            </div>
          </div>
        </div>
        <!--  -->

        <!--  -->
        <div class="signList">
          <div class="signTitle"></div>
          <div class="signTable">
            <div class="tableHeader">
              <div class="tableCell index"></div>
              <div class="tableCell homeNum"></div>
              <div class="tableCell account"></div>
              <div class="tableCell signer"></div>
              <div class="tableCell signMachine"></div>
              <div class="tableCell signTeam"></div>
              <!--<div class="tableCell signDoctor"></div>-->
              <div class="tableCell signDate"></div>
              <div class="tableCell approvalDate"></div>
              <!--<div class="tableCell signChanel"></div>-->
              <div class="tableCell state"></div>
              <div class="tableCell handle"></div>
            </div>
            <div class="tableRow" v-for="(item, index) in dataList" :key="index">
              <div class="tableCell index">
                <div>{{index+1}}</div>
              </div>
              <div class="tableCell homeNum">
                <div>{{item.JTIDSTR}}</div>
              </div>
              <div class="tableCell account">
                <div>{{item.HZXM}}</div>
              </div>
              <div class="tableCell signer">
                <div><span v-for="(val, key) in item.JTCYLB" :key="key">{{val.CYXM}}</span></div>
              </div>
              <div class="tableCell signMachine">
                <div>{{item.QYJGMC}}</div>
              </div>
              <div class="tableCell signTeam">
                <div>{{item.TDMC}}</div>
              </div>
              <!--<div class="tableCell signDoctor">-->
              <!--<div>{{item.DOCTORNAME}}</div>-->
              <!--</div>-->
              <div class="tableCell signDate">
                <div>{{item.SQRQ}}</div>
              </div>
              <div class="tableCell approvalDate">
                <div>{{item.AUDITTIME}}</div>
              </div>
              <!--<div class="tableCell signChanel">-->
              <!--<div>{{item.QYLY}}</div>-->
              <!--</div>-->
              <div class="tableCell state">
                <div>{{item.ORDERSTATUSNAME}}</div>
              </div>
              <div class="tableCell handle">
                <div class="tag" v-if="sign_check" @click.stop="handleDetailInfo(item.JTID, item.ORDERSTATUS, item.QYID)">
                  <div class="icon" :style="{backgroundImage: 'url('+iconImg +')'}"></div>
                  <span></span>
                </div>
                <div class="tag disabled" v-if="!sign_check">
                  <span></span>
                </div>
              </div>
            </div>
          </div>
          <div class="noneData" v-if="dataList.length === 0">
            
          </div>
          <commonDialog
            :isShowText="false"
            :toggleMainContent='toggleMainContent'
            :show='isShowCommonDialog'
          >
            <assignDialog
              :JTID="JTID"
              :ORDERSTATUS="ORDERSTATUS"
              :QYID="QYID"
              :toggleMainContent="toggleMainContent"
            >
            </assignDialog>
          </commonDialog>
        </div>
        <!--  -->
      </div>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNo"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          layout=" prev, pager, next,total, sizes, jumper"
          background
          :total="totalCount">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import * as FileSaver from 'file-saver';
  import Excel from 'exceljs';
  import $ from 'jquery';
  import commonSelector from '@/components/commonSelector';               // 
  import commonTextInput from '@/components/commonTextInput';             // 
  import commonDatePicker from '@/components/commonDatePicker';           // 
  import commonDialog from '@/components/commonDialog';                   // 
  import assignDialog from '@/components/assignDialog';                  // 
  import iconImg from '@/assets/btn_see.png';
  import util from '@/common/js/util';

  const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
  const ONE_DAY = 24*60*60*1000;                                         // 
  let SEARCH_OBJ = {};                                                   // 

  export default {
    name: "signManage",
    components: {
      commonTextInput,
      commonSelector,
      commonDatePicker,
      commonDialog,
      assignDialog
    },
    data() {
      return {
        isShowCommonDialog: false,             // 
        iconImg,
        provinceId: '',                        // id
        provinceOptions: [],                   // list
        cityId: '',                            // id
        cityOptions: [],                       // list
        areaId: '',                             // id
        areaOptions: [],                       // List
        accountVal: '',                         // 
        applyDateVal: '',                      // 
        applyPickerOptions: {
          disabledDate: (time) => {
            let nowData = new Date();
            if (this.applyEndDateVal) {
              // 
              let approvalDate = new Date(this.applyEndDateVal);
              return time > nowData || time > approvalDate
            } else {
              // 
              return time > nowData
            }
          }
        },
        applyEndDateVal: '',                    // 
        applyEndPickerOptions: {
          disabledDate: (time) => {
            let nowData = new Date();
            if (this.applyDateVal) {
              // 
              let applyDate = new Date(this.applyDateVal);
              return time > nowData || (time < applyDate && !(applyDate.getTime() - time.getTime() > 0 && applyDate.getTime() - time.getTime() < ONE_DAY))
            } else {
              // 
              return time > nowData
            }
          }
        },
        medicalInsVal: '',                     // 
        identityCardVal: '',                   // 
        approvalDateVal: '',                   // 
        approvalPickerOptions: {
          disabledDate: (time) => {
            let nowData = new Date();
            if (this.approvalEndDateVal) {
              // 
              let approvalDate = new Date(this.approvalEndDateVal);
              return time > nowData || time > approvalDate
            } else {
              // 
              return time > nowData
            }
          }
        },
        approvalEndDateVal: '',                // 
        approvalEndPickerOptions: {
          disabledDate: (time) => {
            let nowData = new Date();
            if (this.approvalDateVal) {
              // 
              let applyDate = new Date(this.approvalDateVal);
              return time > nowData || (time < applyDate && !(applyDate.getTime() - time.getTime() > 0 && applyDate.getTime() - time.getTime() < ONE_DAY))
            } else {
              // 
              return time > nowData
            }
          }
        },
        doctorTeamVal: '',                     // 
        doctorTeamId: '',                      // Id
        signNameVal: '',                       // 
        signSate: '',                          // 
        doctorName: '',                        // 
        signIdentityCardVal: '',               // 
        stateList: [                            // List
          {
            value: null,
            label: ''
          },
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          },
          {
            value: 2,
            label: ''
          },
          {
            value: 3,
            label: ''
          },
          {
            value: 4,
            label: ''
          },
          {
            value: 5,
            label: ''
          }
        ],
        // signChannelVal: '',                   // 
        // signChannelList: [                     // List
        //   {
        //     value: '0',
        //     label: ''
        //   },
        //   {
        //     value: '1',
        //     label: ''
        //   }
        // ],
        personCategoryVal: '',                 // 
        personCategoryList: [                   // List
        ],
        JTID: '',                              // Id
        QYID: '',                              // Id
        pageSize: 10,                          // 
        pageNo: 1,                            // 
        totalCount: 0,                         // 
        dataList: [],                          // List
        sign_check: false,                    // 
        ORDERSTATUS: ''                      // 
      }
    },
    watch:{
      provinceId(val){
        if(val){
          this.getRegion(val, (data)=>{
            this.cityOptions = data.map(item=>{
              return {
                label: item.name,
                value: item.unitId
              }
            });
          })
        }else{
          this.cityId = '';
          this.areaId = '';
          this.cityOptions = [];
          this.areaOptions = [];
        }
      },
      cityId(val){
        if(val){
          this.getRegion(val, (data)=>{
            this.areaOptions = data.map(item=>{
              return {
                label: item.name,
                value: item.unitId
              }
            });
          })
        }
      }
    },
    methods: {
      /**
       * 
       * @param fag
       */
      toggleMainContent(fag) {
        if (fag === -1) {
          this.isShowCommonDialog = false;
          // $('body').css('overflow-y', 'auto');
        } else {
          this.isShowCommonDialog = !this.isShowCommonDialog;
          // 
          // if (this.isShowCommonDialog) {
          //   $('body').css('overflow-y', 'hidden');
          // } else {
          //   $('body').css('overflow-y', 'auto');
          // }
        }
        if (!this.isShowCommonDialog) {
          this.initData()
        }
      },
      /**
       * 
       */
      searchSignList() {
        this.initData()
      },
      /**
       * 
       * @param parentId
       * @param callback
       */
      getRegion(parentId, callback) {
        this.$post('region/getChild', {parentId}).then(rsp => {
          callback(rsp.datas)
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * Excel
       */
      printExcel() {
        let workbook = new Excel.Workbook();
        // 
        workbook.creator = "Me";
        workbook.lastModifiedBy = "Her";
        workbook.created = new Date(1985, 8, 30);
        workbook.modified = new Date();
        workbook.lastPrinted = new Date(2016, 9, 27);
        //  Excel
        workbook.views = [
          {
            x: 0,
            y: 0,
            width: 1000,
            height: 2000,
            firstSheet: 0,
            activeTab: 1,
            visibility: "visible"
          }
        ];

        // 
        let worksheet = workbook.addWorksheet("", {
          pageSetup: {paperSize: 9, orientation: 'landscape'}
        });
        worksheet.columns = [
          {header: '', key: 'index', width: 8},
          {header: '', key: 'JTID', width: 50},
          {header: '', key: 'HZXM', width: 20},
          {header: '', key: 'CYXM', width: 20},
          {header: '', key: 'QYJGMC', width: 20},
          {header: '', key: 'TDMC', width: 16.63},
          {header: '', key: 'SQRQ', width: 20},
          {header: '', key: 'AUDITTIME', width: 20},
          {header: '', key: 'ORDERSTATUSNAME', width: 16.63}
        ];
        worksheet.getRow(1).values = ['', '', '', '', '', '', '', '', ''];
        worksheet.getRow(1).alignment = {vertical: 'center', horizontal: 'center'};

        this.dataList.forEach((item, index)=>{
          let rowValues =[];
          let strArr = item.JTCYLB.map(item=>{
            return item.CYXM;
          });
          rowValues[0] = index+1;
          rowValues[1] = item.JTID;
          rowValues[2] = item.HZXM;
          rowValues[3] = strArr.join(",");
          rowValues[4] = item.QYJGMC;
          rowValues[5] = item.TDMC;
          rowValues[6] = item.SQRQ;
          rowValues[7] = item.AUDITTIME;
          rowValues[8] = item.ORDERSTATUSNAME;
          worksheet.getRow(index+2).values = rowValues;
          worksheet.getRow(index+2).alignment = {vertical: 'center', horizontal: 'center'};
        });
        workbook.xlsx.writeBuffer().then((data) => {
          const blob = new Blob([data], {type: EXCEL_TYPE});
          FileSaver.saveAs(blob, 'download.xlsx');
        })
      },
      /**
       * 
       * @param val 
       */
      initData() {
        const {
          provinceId, cityId, areaId, medicalInsVal, doctorTeamId, doctorName, signNameVal,
          accountVal, identityCardVal, personCategoryVal, signIdentityCardVal, doctorTeamVal,
          pageSize, pageNo, applyDateVal, applyEndDateVal, approvalDateVal, approvalEndDateVal, signSate
        } = this;
        // 
        this.pageNo = 1;
        Object.assign(SEARCH_OBJ, this);
        this.$post('family/sign/order/getPage', {
          PROVINCEID: provinceId,
          CITYID: cityId,
          DISTRICTID: areaId,
          ORGNAME: medicalInsVal,
          // TEAMID: doctorTeamId,
          TEAMNAME: doctorTeamVal,
          DOCTORNAME: doctorName,
          HZXM: accountVal,
          HZSFZJH: identityCardVal,
          CYXM: signNameVal,
          CYSFZJH: signIdentityCardVal,
          // RQFLID: personCategoryVal,
          BEGINSQRQ: applyDateVal,
          ENDSQRQ: applyEndDateVal,
          BEGINSPRQ: approvalDateVal,
          ENDSPRQ: approvalEndDateVal,
          ORDERSTATUS: signSate,
          pageSize,
          pageNo: 1
        }).then(rsp => {
          this.totalCount = rsp.total;
          // 
          rsp.datas.forEach(item=>{
            item.JTIDSTR = util.secretData(item.JTID, 'num');
          });
          this.dataList = rsp.datas;
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       * @param val 
       */
      pageNoSearch(val){
        const {
          provinceId, cityId, areaId, medicalInsVal, doctorTeamId, doctorName, signNameVal,
          accountVal, identityCardVal, personCategoryVal, signIdentityCardVal, doctorTeamVal,
          pageSize, pageNo, applyDateVal, applyEndDateVal, approvalDateVal, approvalEndDateVal, signSate
        } = SEARCH_OBJ;
        this.$post('family/sign/order/getPage', {
          PROVINCEID: provinceId,
          CITYID: cityId,
          DISTRICTID: areaId,
          ORGNAME: medicalInsVal,
          // TEAMID: doctorTeamId,
          TEAMNAME: doctorTeamVal,
          DOCTORNAME: doctorName,
          HZXM: accountVal,
          HZSFZJH: identityCardVal,
          CYXM: signNameVal,
          CYSFZJH: signIdentityCardVal,
          // RQFLID: personCategoryVal,
          BEGINSQRQ: applyDateVal,
          ENDSQRQ: applyEndDateVal,
          BEGINSPRQ: approvalDateVal,
          ENDSPRQ: approvalEndDateVal,
          ORDERSTATUS: signSate,
          pageSize,
          pageNo: val
        }).then(rsp => {
          this.totalCount = rsp.total;
          // 
          rsp.datas.forEach(item=>{
            item.JTIDSTR = util.secretData(item.JTID, 'num');
          });
          this.dataList = rsp.datas;
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * pageSize
       * @param val
       */
      handleSizeChange(val) {
        this.pageSize = val;
        this.initData();
      },
      /**
       * 
       */
      handleCurrentChange(val) {
        this.pageNo = val;
        this.pageNoSearch(val);
      },
      /**
       * 
       * @param JTID  Id
       * @param ORDERSTATUS 
       * @param QYID Id
       */
      handleDetailInfo(JTID, ORDERSTATUS, QYID) {
        this.JTID = JTID;
        this.ORDERSTATUS = ORDERSTATUS;
        this.QYID = QYID;
        this.toggleMainContent()
      }
    },
    created() {
      this.initData();
      let authObj = JSON.parse(this.$localStore.getItem('authority'));
      // 
      if (!!authObj) {
        this.sign_check = authObj['sign_check'];
      }
      this.getRegion('', (data)=>{
        this.provinceOptions = data.map(item=>{
          return {
            label: item.name,
            value: item.unitId
          }
        });
        this.provinceOptions.unshift({
          label: '',
          value: ''
        })
      })
    }
  }
</script>

<style lang="scss">
  @import '../style/signManage.scss';
</style>



