<template>
  <div class="memberManage">
    <div class="flexBox">
      <div>
        <div class="memberSearch">
          <commonTextInput :text="''" v-model="orgName"></commonTextInput>
          <commonTextInput :text="''" v-model="doctorName"></commonTextInput>
          <commonTextInput :text="''" v-model="doctorCode"></commonTextInput>
          <!--<commonSelector :text="''" v-model="posTitleName"></commonSelector>-->
          <el-button type="primary" class="major" @click.stop.prevent="searchMember"></el-button>
        </div>
        <div class="roleManageContent">
          <div class="personTitle">
            <p class="title"></p>
          </div>
          <div class="personContent">
            <div class="personTable">
              <div class="tableHeader">
                <div class="tableCell index"></div>
                <div class="tableCell name"></div>
                <div class="tableCell code"></div>
                <div class="tableCell gender"></div>
                <!--<div class="tableCell job"></div>-->
                <div class="tableCell organization"></div>
                <div class="tableCell handle"></div>
              </div>
              <div class="tableRow" v-for="(item, index) in dataList" :key="index">
                <div class="tableCell">{{index+1}}</div>
                <div class="tableCell">{{item.doctorName}}</div>
                <div class="tableCell">{{item.doctorCode}}</div>
                <div class="tableCell">{{item.sexName}}</div>
                <!--<div class="tableCell">{{item.posTitleName}}</div>-->
                <div class="tableCell">{{item.orgName}}</div>
                <div class="tableCell">
                  <el-button type="primary" v-if="team_member_edit" class="major"
                             @click.prevent.stop="searchOneMember(item.doctorId)">
                  </el-button>
                  <span v-if="!team_member_edit"></span>
                </div>
              </div>
            </div>
            <div class="noneData" v-if="dataList.length === 0">
              
            </div>
          </div>
        </div>
      </div>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNo"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          layout=" prev, pager, next,total, sizes, jumper"
          background
          :total="totalCount">
        </el-pagination>
      </div>
    </div>
    <!--  -->
    <editRoleMember
      :id="memberId"
      :toggleMainContent='toggleMainContent'
      :show='isShowEditDialog'
    >

    </editRoleMember>
    <!--  -->
  </div>
</template>

<script>
  import commonTextInput from '@/components/commonTextInput';
  import commonSelector from '@/components/commonSelector';
  import editRoleMember from '@/components/editRoleMember';                        // 

  let SEARCH_OBJ = {};                                                   // 

  export default {
    name: "memberManage",
    components: {
      commonTextInput,
      commonSelector,
      editRoleMember
    },
    data() {
      return {
        isShowEditDialog: false,          // 
        memberId: '',                     // id
        orgName: '',                      // 
        doctorName: '',                   // 
        doctorCode: '',                   // 
        // posTitleName: '',                 // 
        pageNo: 1,                        // 
        pageSize: 10,                     // 
        totalCount: 0,                    // 
        dataList: [],                     // 
        team_member_edit: false           // 
      }
    },
    methods: {
      /**
       * 
       * @param fag
       */
      toggleMainContent(fag) {
        if (fag === -1) {
          this.isShowEditDialog = false;
        } else {
          this.isShowEditDialog = !this.isShowEditDialog;
        }
      },
      /**
       * 
       * @param id id
       */
      searchOneMember(id) {
        this.memberId = id;
        this.toggleMainContent();
      },
      /**
       * 
       */
      searchMember() {
        const {orgName, doctorName, doctorCode, posTitleName, pageSize} = this;
        // posTitleName,
        Object.assign(SEARCH_OBJ, this);
        // 
        this.pageNo = 1;
        this.$post('doctor/getPage', {orgName, doctorName, doctorCode,  pageNo: 1, pageSize}).then(rsp => {
          this.totalCount = rsp.total;
          this.dataList = rsp.datas;
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       * @param val 
       */
      pageNoSearch(val){
        const {orgName, doctorName, doctorCode, posTitleName, pageSize} = SEARCH_OBJ;
        // posTitleName,
        this.$post('doctor/getPage', {orgName, doctorName, doctorCode,  pageNo: val, pageSize}).then(rsp => {
          this.totalCount = rsp.total;
          this.dataList = rsp.datas;
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * pageSize
       * @param val
       */
      handleSizeChange(val) {
        this.pageSize = val;
        this.searchMember();
      },
      /**
       * 
       */
      handleCurrentChange(val) {
        this.pageNo = val;
        this.pageNoSearch(val);
      }
    },
    created() {
      this.searchMember();
      let authObj = JSON.parse(this.$localStore.getItem('authority'));
      // 
      if(authObj){
        this.team_member_edit = authObj['team_member_edit'];
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../style/memberManage.scss';
</style>



