<template>
  <section class="a4">
    <h1 class="title"></h1>
    <div class="sign-begin">
      <p><span>{{PARTYA}}</span></p>
      <p><span>{{PARTYB}}</span></p>
      <p><span class="idCard"><i>{{SFZJH}}</i></span> <span><i>{{LXDH}}</i></span></p>
      <p><span>{{JTZZ}}</span></p>
    </div>
    <div class="sign-content-1">
      <p><span class="decoration">{{TDMC}}</span></p>
      <p><span>{{TDCYList}}</span></p>
      <p><span>{{TDZRYS}}</span></p>
      <p>{{TDLXDH}}</p>
    </div>
    <div class="sign-content-2">
      <p></p>
      <p></p>
      <div v-for="(item, index) in QYFWList" :key="index" class="servicePackage">
        <p>{{index+1}}<span class="decoration">{{item.XM}}</span><span class="decoration">{{item.FWBMCList}}</span> </p>
        <div class="service-item">
          <div class="item" v-for="(val, i) in item.FWBList" :key="i">
            <p>{{val.FWBMC}}</p>
            <div class="services">
              <table class="servicePackages">
                <tbody>
                <tr>
                  <th class="order"></th>
                  <th class="category"></th>
                  <th class="service-num"></th>
                  <th class="service-explain"></th>
                  <th class="note"></th>
                </tr>
                <tr v-for="(v,k) in val.FWXMList">
                  <td>{{k+1}}</td>
                  <td>{{v.FWXMMC}}</td>
                  <td>{{v.FWCS}}</td>
                  <td class="description">{{v.FWXMMS}}</td>
                  <td></td>
                </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
  </div>
  <div class="sign-content-3">
    <p></p>
    <p>1.</p>
    <p>2.</p>
    <p>3.</p>
    <p>4.</p>
    <p>
      </p>
    <p></p>
  </div>
  <div class="sign-member">
    <p>
      </p>
    <div class="sign-member-content">
      <p></p>
      <div class="family-member">
        <table class="family-member-table">
          <tbody>
          <tr>
            <th class="index"></th>
            <th class="relation"></th>
            <th class="name"></th>
            <th class="gender"></th>
            <th class="idCard"></th>
            <th class="category"></th>
          </tr>

          <tr v-for="(item, index) in JTCYList" :key="index">
            <td>
              {{index < 9 ? '0'+ (index+1) : (index+1)}}
            </td>
            <td>{{item.YQYDBGXMC}}</td>
            <td>{{item.XM}}</td>
            <td>{{item.XB}}</td>
            <td>{{item.SFZJH}}</td>
            <td>{{item.RQFLMC}}</td>
          </tr>

          </tbody>
        </table>
      </div>
    </div>
  </div>
  <div class="sign-check">
    <p class="left-common-check"></p>
    <div class="signer">
      <span class="response-doctor"></span>
      <span></span>
    </div>
    <div class="sign-time">
      <span class="left-time"><i class="year"></i><i class="month"></i><i class="day"></i></span>
      <span class="right-time"><i class="year"></i><i class="month"></i><i class="day"></i></span>
    </div>
    <p class="contract-time"></p>
    <p class="contract-reason"></p>
    <div class="contract-signer">
      <span class="response-doctor"></span>
      <span></span>
    </div>
  </div>
  </section>
</template>

<script>
  export default {
    name: "pdfPrint",
    props: {
      PARTYA: {default: ''},
      PARTYB: {default: ''},
      SFZJH: {default: ''},
      LXDH: {default: ''},
      JTZZ: {default: ''},
      TDMC: {default: ''},
      TDCYList: {default: ''},
      TDZRYS: {default: ''},
      TDLXDH: {default: ''},
      QYFWList: {default: ()=> []},
      JTCYList: {default: ()=>[]}
    },
    methods: {

    },
    mounted() {

    }
  }
</script>

<style scoped>
  *{
    box-sizing: border-box;
  }
  @page { margin-top: 0; margin-bottom: 0;}
  p {
    margin-top: 0;
    margin-bottom: 0;
  }
  table{
    margin: auto;
    border-bottom: none;
    border-left: none;
    font-family: SimSun;
  }

  .justify {
    text-align: justify;
  }

  .a4 {
    font-size: 4mm;
    font-family: SimHei;
    color: #262626;
    background-color: aliceblue;
    margin: 0 auto;
    width: 279mm;
    height: 216mm;
  }

  .title {
    padding: 10mm 0;
    font-size: 8mm;
    font-weight: bold;
    text-align: center;
  }

  .sign-begin p, .sign-content-1 p, .sign-content-2 p, .sign-content-3 p, .sign-member p {
    line-height: 10mm;
  }

  .idCard, .response-doctor, .left-time {
    margin-right: 100mm;
  }

  .sign-content-1 {
    margin: 5mm 0;
  }

  .service-item {
    padding-top: 5mm;
  }
  .servicePackage{
    min-height: 50mm;
  }

  .sign-content-3 {
    margin: 10mm 0;
  }

  .left-common-check {
    padding-top: 20mm;
    padding-bottom: 30mm;
  }

  .signer, .sign-time {
    padding-bottom: 30mm;
  }
  .signer{
    padding-top: 5mm;
  }
  .year {
    width: 10mm;
    display: inline-block;
  }

  .month, .day {
    width: 5mm;
    display: inline-block;
  }

  .contract-time, .contract-reason {
    padding-bottom: 10mm;
  }

  .contract-signer {
    padding-bottom: 30mm;
  }
  .services{
    margin: 0 10mm;
  }

  .servicePackages {
    width: 100%;
    border: 1px solid #262626;
    border-collapse: collapse;
  }

  .servicePackages th, .servicePackages td {
    border: 1px solid #262626;
    padding: 2mm;
    text-align: center;
    vertical-align: middle;
    box-sizing: border-box;
  }

  .order {
    width: 10%;
  }

  .category {
    width: 30%;
  }

  .service-num {
    width: 10%;
  }

  .service-explain {
    width: 40%;
  }

  .note {
    width: 10%;
  }

  .servicePackages .description {
    text-align: left;
  }

  .family-member{
    margin: 0 10mm;
    min-height: 50mm;
  }
  .family-member-table {
    width: 100%;
    border: 1px solid #262626;
    border-collapse: collapse;
  }

  .family-member-table th, .family-member-table td {
    border: 1px solid #262626;
    line-height: 10mm;
    text-align: center;
    vertical-align: middle;
  }

  .index {
    width: 10%;
  }

  .relation {
    width: 20%;
  }

  .name {
    width: 10%;
  }

  .gender {
    width: 10%;
  }

  .idCard {
    width: 30%;
  }

  .category {
    width: 20%;
  }
  .decoration{
    text-decoration: underline;
  }
  .sign-member-content{
    padding: 5mm 0;
  }
  .sign-check{
    padding-top: 5mm;
  }
</style>



