<template>
  <!--  -->
  <div class="accountManage">
    <div class="flexBox">
      <div>
        <!--<div class="accountSearch">-->
        <!--<commonTextInput :text="''" v-model="orgName"></commonTextInput>-->
        <!--<commonTextInput :text="''" v-model="doctorName"></commonTextInput>-->
        <!--<commonTextInput :text="''" v-model="account"></commonTextInput>-->
        <!--<commonSelector :text="''" v-model="roleId" :options="typeList"></commonSelector>-->
        <!--<commonSelector :text="''" v-model="isEnabled" :options="stateList"></commonSelector>-->
        <!--<el-button type="primary" class="major" @click.stop.prevent="initData"></el-button>-->
        <!--</div>-->
        <div class="accountManageContent">
          <div class="personTitle">
            <p class="title"></p>
            <el-button type="primary" v-if="account_add" class="major" @click.prevent.stop="openDialog('')">
              
            </el-button>
          </div>
          <div class="personContent">
            <div class="personTable">
              <div class="tableHeader">
                <div class="tableCell index"></div>
                <div class="tableCell name"></div>
                <div class="tableCell account"></div>
                <div class="tableCell nickname"></div>
                <div class="tableCell range"></div>
                <div class="tableCell state"></div>
                <div class="tableCell handle"></div>
              </div>
              <div class="filterDate"></div>
              <div class="tableRow" v-for="(item, index) in dataList" :key="index">
                <div class="tableCell">{{index+1}}</div>
                <div class="tableCell">{{item.userName}}</div>
                <div class="tableCell">{{item.account}}</div>
                <div class="tableCell">{{item.roleName}}</div>
                <div class="tableCell">
                  <span v-if="item.roleId === 6"></span>
                  <span v-if="item.roleId !== 6 && item.roleId !== 7">
                {{
                (item.provinceName && item.cityName && item.districtName) ? item.provinceName + item.cityName + item.districtName : (item.provinceName && item.cityName ? item.provinceName + item.cityName : item.provinceName )
                }}
              </span>
                  <span v-if="item.roleId === 7"></span>
                </div>
                <div class="tableCell">{{item.isEnable ? '' : ''}}</div>
                <div class="tableCell">
                  <el-button type="primary" v-if="account_edit" class="major" @click.prevent.stop="openDialog(item.userId)">
                    
                  </el-button>
                  <el-button type="primary" v-if="account_edit" class="major"
                             @click.prevent.stop="setEnable(!item.isEnable, item.userId)">
                    {{item.isEnable ? '' : ''}}
                  </el-button>
                  <el-button type="danger" v-if="account_delete" class="major"
                             @click.prevent.stop="readyToDelete(item.userId)">
                  </el-button>
                  <span v-if="!account_edit  && !account_delete"></span>
                </div>
              </div>
            </div>
            <div class="noneData" v-if="dataList.length === 0">
              
            </div>

          </div>
        </div>
      </div>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNo"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          layout=" prev, pager, next,total, sizes, jumper"
          background
          :total="totalCount">
        </el-pagination>
      </div>
    </div>
    <!--  -->
    <commonDialog
      :toggleMainContent='toggleMainContent'
      :show='isShowCommonDialog'
      :isShowText='false'
    >
      <addNewAccount
        :userId="userId"
        :toggleContent="toggleMainContent"
      >
      </addNewAccount>
    </commonDialog>
    <!--  -->

    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowAccount"
      :toggleContent="toggleAccountDialog"
      :confirmDialog="confirmDelete"
    >
      <div class="paddingBox"></div>
    </assignAlertDialog>
    <!--  -->
  </div>

  <!--  -->
</template>

<script>
  import addNewAccount from '@/components/addNewAccount';
  import commonDialog from '@/components/commonDialog';
  import assignAlertDialog from '@/components/assignAlertDialog';                  // /
  import commonTextInput from '@/components/commonTextInput';
  import commonSelector from '@/components/commonSelector';

  let SEARCH_OBJ = {};                                                   // 

  export default {
    name: "accountManage",
    data() {
      return {
        isShowCommonDialog: false,      // 
        isShowAccount: false,
        account_add: false,             // 
        account_edit: false,            // 
        account_delete: false,          // 
        userId: '',                     // Id
        dataList: [],                   // List
        orgName: '',                    // 
        doctorName: '',                 // 
        account: '',                    // 
        roleId: '',                   // Id
        typeList: [],                   // List
        isEnabled: '',                  // 
        stateList: [                    // List
          {label: '', value: ''},
          {
            label: '',
            value: true
          },
          {
            label: '',
            value: false
          }
        ],
        pageNo: 1,                      // 
        pageSize: 10,                   // 
        totalCount: 0                   // 
      }
    },
    components: {
      addNewAccount,
      commonDialog,
      assignAlertDialog,
      commonTextInput,
      commonSelector
    },
    methods: {
      /**
       * /
       * @param fag
       */
      toggleMainContent(fag) {
        if (fag === -1) {
          this.isShowCommonDialog = false;
        } else {
          this.isShowCommonDialog = !this.isShowCommonDialog;
        }
        if(!this.isShowCommonDialog){
          this.initData()
        }
      },
      /**
       * 
       * @param fag
       **/
      toggleAccountDialog(fag) {
        if (fag === -1) {
          this.isShowAccount = false;
        } else {
          this.isShowAccount = !this.isShowAccount;
        }
      },
      /**
       * 
       * @param userId
       */
      readyToDelete(userId) {
        this.userId = userId;
        this.toggleAccountDialog()
      },
      /**
       * 
       */
      confirmDelete() {
        const {userId} =this;
        this.$post('user/delete', {
          userId
        }).then(rsp => {
          this.initData();
          this.toggleAccountDialog(-1)
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * /
       * @param flag type:boolean true/false
       * @param userId type:string
       */
      setEnable(flag, userId) {
        this.$post('user/setEnable', {
          userId,
          isEnabled: flag
        }).then(rsp => {
          this.initData()
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       * @param userId
       */
      openDialog(userId) {
        this.userId = userId;
        this.toggleMainContent()
      },
      /**
       * 
       */
      initData() {
        const {orgName, doctorName, account, roleId, isEnabled, pageSize} = this;
        Object.assign(SEARCH_OBJ, this);
        // 
        this.pageNo = 1;
        this.$post('user/getPage', {
          orgName, doctorName, account, roleId, isEnabled, pageNo: 1, pageSize
        }).then(rsp => {
          this.totalCount = rsp.total;
          this.dataList = rsp.datas;
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       * @param val 
       */
      pageNoSearch(val){
        const {orgName, doctorName, account, roleId, isEnabled, pageSize} = SEARCH_OBJ;
        this.$post('user/getPage', {
          orgName, doctorName, account, roleId, isEnabled, pageNo: val, pageSize
        }).then(rsp => {
          this.totalCount = rsp.total;
          this.dataList = rsp.datas;
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * pageSize
       * @param val
       */
      handleSizeChange(val) {
        this.pageSize = val;
        this.initData();
      },
      /**
       * 
       */
      handleCurrentChange(val) {
        this.pageNo = val;
        this.pageNoSearch(val);
      }
    },
    created() {
      let authObj = JSON.parse(this.$localStore.getItem('authority'));
      // 
      if(authObj){
        this.account_add = authObj['account_add'];
        this.account_edit = authObj['account_edit'];
        this.account_delete = authObj['account_delete'];
      }
      this.initData();
      // 
      this.$post('user/role/getPage', {
        pageNo: 1,
        pageSize: 100
      }).then(rsp => {
        this.typeList = rsp.datas.map(item=>{
          return {
            label: item.roleName,
            value: item.roleId
          }
        });
      }, rej => {
        if(rej.data.errcode === 460){
          this.$message.error(rej.data.datas[0].message);
        }else{
          this.$message.error(rej.data.errmsg);
        }})
    }
  }
</script>

<style lang="scss">
  @import '../style/accountManage.scss';
</style>



