<template>
  <div class="teamManage">
    <div class="flexBox">
      <div>
        <div class="teamSearch">
          <commonTextInput :text="''" v-model="orgName"></commonTextInput>
          <commonTextInput :text="''" v-model="TDMC"></commonTextInput>
          <commonTextInput :text="''" v-model="YSXM"></commonTextInput>
          <commonSelector :text="''" v-model="ISENABLED" :options="stateList"></commonSelector>
          <el-button type="primary" class="major" @click.stop.prevent="searchTeamInfo"></el-button>
        </div>
        <div class="roleManageContent">
          <div class="personTitle">
            <p class="title"></p>
            <div class="functionBtns">
              <!--<el-button type="success" class="major" v-if="team_upload" @click.stop.prevent=""></el-button>-->
              <el-button type="primary" class="major" v-if="team_add" @click.stop.prevent="openTeamDialog('')">
              </el-button>
            </div>
          </div>
          <div class="personContent">
            <div class="personTable">
              <div class="tableHeader">
                <div class="tableCell index"></div>
                <div class="tableCell name"></div>
                <!--<div class="tableCell code"></div>-->
                <div class="tableCell organization"></div>
                <div class="tableCell leader"></div>
                <div class="tableCell count"></div>
                <div class="tableCell state"></div>
                <div class="tableCell handle"></div>
              </div>
              <div class="tableRow" v-for="(item, index) in dataList" :key="index">
                <div class="tableCell">{{index+1}}</div>
                <div class="tableCell">{{item.TDMC}}</div>
                <!--<div class="tableCell"></div>-->
                <div class="tableCell">{{item.ORGNAME}}</div>
                <div class="tableCell">{{item.DZXM}}</div>
                <div class="tableCell">{{item.CYSL}}</div>
                <div class="tableCell">{{item.ISENABLED ? '': ''}}</div>
                <div class="tableCell">
                  <el-button type="primary" class="major" v-if="team_edit" @click.prevent.stop="openTeamDialog(item.TDID)">
                    
                  </el-button>
                  <el-button type="primary" class="major" v-if="team_edit"
                             @click.prevent.stop="openMemberDialog(item.TDID, item.ORGID)">
                  </el-button>
                  <el-button type="primary" class="major" v-if="team_edit"
                             @click.prevent.stop="disabledTeam(item.TDID, !item.ISENABLED)">
                    {{item.ISENABLED ? '': ''}}
                  </el-button>
                  <el-button type="danger" class="major" v-if="team_delete" @click.prevent.stop="handleDelete(item.TDID)">
                    
                  </el-button>
                  <span v-if="!team_delete && !team_edit"></span>
                </div>
              </div>
            </div>
            <div class="noneData" v-if="dataList.length === 0">
              
            </div>
          </div>
        </div>
      </div>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNo"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          layout=" prev, pager, next,total, sizes, jumper"
          background
          :total="totalCount">
        </el-pagination>
      </div>
    </div>
    <!--  -->
    <assignAlertDialog
      :title="''"
      :isShowDialog="isShowTeam"
      :toggleContent="toggleTeamDialog"
      :confirmDialog="confirmDelete"
    >
      <div class="paddingBox">
        <commonTextArea v-model="ZXYY" :text="''"></commonTextArea>
      </div>
    </assignAlertDialog>
    <!--  -->

    <!--  -->
    <commonDialog
      :toggleMainContent='toggleMainContent'
      :show='isShowCommonDialog'
      :isShowText='false'
    >
      <addNewTeam
        :teamId="teamId"
        :toggleContent="toggleMainContent"
        @regetSource="searchTeamInfo"
        @openMember="openMemberDialog"
      >
      </addNewTeam>
    </commonDialog>
    <!--  -->

    <!--  -->
    <commonDialog
      :toggleMainContent='toggleMemberContent'
      :show='isShowMemberDialog'
      :isShowText='false'
    >
      <addNewTeamMember
        :teamId="teamId"
        :orgId="orgId"
        :toggleContent="toggleMemberContent"
        @regetSource="searchTeamInfo"
      >
      </addNewTeamMember>
    </commonDialog>
    <!--  -->
  </div>
</template>

<script>
  import commonTextInput from '@/components/commonTextInput';
  import commonSelector from '@/components/commonSelector';
  import assignAlertDialog from '@/components/assignAlertDialog';                  // /
  import addNewTeam from '@/components/addNewTeam';                                // 
  import commonDialog from '@/components/commonDialog';                            // 
  import addNewTeamMember from '@/components/addNewTeamMember';                   // 
  import commonTextArea from '@/components/commonTextArea';                       // 

  let SEARCH_OBJ = {};                                                            // 

  export default {
    name: "teamManage",
    data() {
      return {
        isShowCommonDialog: false,                      // 
        isShowMemberDialog: false,                      // 
        isShowTeam: false,                              // 
        teamId: '',                                     // Id
        orgName: '',                                    // 
        orgId: '',                                      // Id
        TDMC: '',                                       // 
        YSXM: '',                                       // 
        ISENABLED: '',                                  // 
        stateList: [
          {label: '', value: ''},
          {
            label: '',
            value: true
          },
          {
            label: '',
            value: false
          }
        ],
        pageSize: 10,
        pageNo: 1,
        totalCount: 0,
        dataList: [],
        deleteId: '',                                   // Id
        team_add: false,                                // 
        team_edit: false,                               // 
        team_delete: false,                             // 
        team_upload: false,                              // 
        ZXYY: '',                                      // 
      }
    },
    components: {
      commonTextInput,
      commonSelector,
      assignAlertDialog,
      addNewTeam,
      commonDialog,
      addNewTeamMember,
      commonTextArea
    },
    methods: {
      /**
       * /
       * @param val type:string
       */
      openTeamDialog(val) {
        this.teamId = val;
        this.toggleMainContent()
      },
      /**
       * /
       * @param val type:string
       * @param val type:string
       */
      openMemberDialog(val, orgId) {
        this.teamId = val;
        this.orgId = orgId;
        this.toggleMemberContent()
      },
      /**
       * 
       * @param fag
       **/
      toggleMainContent(fag) {
        if (fag === -1) {
          this.isShowCommonDialog = false;
        } else {
          this.isShowCommonDialog = !this.isShowCommonDialog;
        }
        if(!this.isShowCommonDialog){
          this.searchTeamInfo();
        }
      },
      /**
       * /
       * @param fag
       **/
      toggleMemberContent(fag) {
        if (fag === -1) {
          this.isShowMemberDialog = false;
        } else {
          this.isShowMemberDialog = !this.isShowMemberDialog;
        }
        if(!this.isShowMemberDialog){
          this.searchTeamInfo();
        }
      },
      /**
       * 
       * @param fag
       */
      toggleTeamDialog(fag) {
        if (fag === -1) {
          this.isShowTeam = false;
        } else {
          this.isShowTeam = !this.isShowTeam;
        }
        if(!this.isShowTeam){
          this.ZXYY = '';
        }
      },
      /**
       * 
       */
      searchTeamInfo() {
        const {orgName, TDMC, YSXM, ISENABLED, pageSize} = this;
        Object.assign(SEARCH_OBJ, this);
        // 
        this.pageNo = 1;
        this.$post('family/teamAndMember/getPage', {
          orgName, TDMC, YSXM, ISENABLED, pageSize, pageNo: 1
        }).then(rsp => {
          this.dataList = rsp.datas;
          this.totalCount = rsp.total;
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       * @param val 
       */
      pageNoSearch(val){
        const {orgName, TDMC, YSXM, ISENABLED, pageSize} = SEARCH_OBJ;
        this.$post('family/teamAndMember/getPage', {
          orgName, TDMC, YSXM, ISENABLED, pageSize, pageNo: val
        }).then(rsp => {
          this.dataList = rsp.datas;
          this.totalCount = rsp.total;
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       */
      confirmDelete() {
        this.$post('family/teamAndMember/deleteTeam', {
          TDID: this.deleteId,
          ZXYY: this.ZXYY
        }).then(rsp => {
          this.searchTeamInfo();
          this.toggleTeamDialog(-1)
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * 
       * @param id
       */
      handleDelete(id) {
        this.deleteId = id;
        this.toggleTeamDialog()
      },
      /**
       * 
       * @param TDID       type: string ID
       * @param ISENABLED  type: boolean 
       */
      disabledTeam(TDID, ISENABLED) {
        this.$post('family/teamAndMember/setTeamEnable', {
          TDID, ISENABLED
        }).then(rsp => {
          this.searchTeamInfo()
        }, rej => {
          if(rej.data.errcode === 460){
            this.$message.error(rej.data.datas[0].message);
          }else{
            this.$message.error(rej.data.errmsg);
          }})
      },
      /**
       * pageSize
       * @param val
       */
      handleSizeChange(val) {
        this.pageSize = val;
        this.searchTeamInfo();
      },
      /**
       * 
       */
      handleCurrentChange(val) {
        this.pageNo = val;
        this.pageNoSearch(val);
      }
    },
    created() {
      this.searchTeamInfo();
      let authObj = JSON.parse(this.$localStore.getItem('authority'));
      // 
      if(!!authObj){
        this.team_add = authObj['team_add'];
        this.team_edit = authObj['team_edit'];
        this.team_delete = authObj['team_delete'];
        this.team_upload = authObj['team_upload'];
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../style/teamManage.scss';
</style>



